import {
	closestDeep,
	querySelectorAllDeep,
	querySelectorDeep,
	DIRECTIONS
} from '@devsnc/library-vtb-commons';

export const BOARD_ARROW_NAVIGABLE_ELEMENTS =
	'sn-card-wrapper,.sn-lane-header,.lane-footer-container,sn-swimlane-header';
// Note: Using SN_CARD_WRAPPER instead of 'div.card-wrapper' unlike header and footer because we previousSibling
// and nextSibling won't work, since .card-wrapper is the only element in its parent

/**
 * Use of lastFocusedLaneItemId variable is to store the last focused lane element's ID, more specifically the last
 * focused lane header or the ID of the lane of last focused card or lane footer. The lastFocusedLaneItemId is used to
 * predict the correct lane/lane-header to which the focus should go when navigating from a swimlane header.
 * For instance, from lane-header with index 3 if we press Down once, focus moves to the first swimlane header and from
 * here if we press Down again, the focus should move to the lane with index 3 in first swimlane and vice versa.
 * This is where lastFocusedLaneItemId comes to use, to match the lane id.
 */
let lastFocusedLaneItemId;

const LANE_FOOTER_CONTAINER = '.lane-footer-container';
const LANE_HEADER = '.lane-header';
const SN_SWIMLANE_HEADER = 'sn-swimlane-header';
const SN_LANE = 'sn-lane';
const SN_LANE_HEADER = 'sn-lane-header';
const SN_CARD_WRAPPER = 'sn-card-wrapper';
const SN_SWIMLANE = 'sn-swimlane';
const NOW_VISUAL_BOARD = 'now-visual-board';
const FOOTER_CONTAINER = '.footer-container';

/**
 * This is to customize the behavior of 'visual-navigation' module to suit the board component's needs.
 * Few customizations we need are:
 * 1. When pressing Down on a card wrapper, the next card should get focused, even if the lane footer is visually closer
 * 2. When pressing Up on a card wrapper, the previous card should get focused, even if the lane header is visually closer
 * 3. When pressing Right/Left, the immediately next and previous lane's elements get more priority than lanes that can
 * be farther away
 */
// TODO: Clean up this garbage code
export const getNavigableElementsByDirection = (
	elementsSelector = BOARD_ARROW_NAVIGABLE_ELEMENTS
) => {
	return (activeElement, direction) => {
		const board = closestDeep(activeElement, NOW_VISUAL_BOARD);
		const swimlaneHeaders = querySelectorAllDeep(
			SN_SWIMLANE_HEADER,
			board,
			false
		);

		// If swimlanes are present, its a 2D board.
		if (swimlaneHeaders.length) {
			return getNavigableElementsByDirectionIn2DBoard(
				activeElement,
				direction,
				swimlaneHeaders,
				elementsSelector,
				board
			);
		} else {
			return getNavigableElementsByDirectionIn1DBoard(
				activeElement,
				direction,
				elementsSelector,
				board
			);
		}
	};
};

const getSwimlaneHeader = (activeElement, swimlaneHeaders) => {
	if (closestDeep(activeElement, SN_LANE)) {
		const swimlane = closestDeep(activeElement, SN_SWIMLANE);
		return querySelectorDeep(SN_SWIMLANE_HEADER, swimlane);
	} else if (closestDeep(activeElement, SN_SWIMLANE_HEADER)) {
		return closestDeep(activeElement, SN_SWIMLANE_HEADER);
	} else if (closestDeep(activeElement, SN_LANE_HEADER)) {
		return swimlaneHeaders[0];
	}
};

const getNextSwimlaneHeader = (currentSwimlaneHeader) => {
	const swimlane = closestDeep(currentSwimlaneHeader, SN_SWIMLANE);
	if (swimlane.nextSibling) {
		return querySelectorDeep(SN_SWIMLANE_HEADER, swimlane.nextSibling);
	}
	return null;
};

const getPreviousSwimlaneHeader = (currentSwimlaneHeader) => {
	const swimlane = closestDeep(currentSwimlaneHeader, SN_SWIMLANE);
	if (swimlane.previousSibling) {
		return querySelectorDeep(SN_SWIMLANE_HEADER, swimlane.previousSibling);
	}
	return null;
};

const setLastFocusedLaneItemId = (activeElement) => {
	if (closestDeep(activeElement, SN_LANE_HEADER)) {
		const currentLaneHeader = closestDeep(activeElement, SN_LANE_HEADER);
		lastFocusedLaneItemId = currentLaneHeader.id;
	} else if (closestDeep(activeElement, SN_LANE)) {
		const currentLane = closestDeep(activeElement, SN_LANE);
		lastFocusedLaneItemId = currentLane.lane.id;
	}
};

const swimlaneHeaderArrowDown = (
	focusableLane,
	nextSwimlaneHeader,
	elementsSelector
) => {
	const focusableElementsinLane = querySelectorAllDeep(
		elementsSelector,
		focusableLane,
		false
	);
	// If lane has navigable elements then focus on last elem in lane else focus on the nextSwimlaneHeader.
	if (focusableElementsinLane.length) {
		return [focusableElementsinLane[0]];
	} else {
		return querySelectorAllDeep(
			elementsSelector,
			nextSwimlaneHeader,
			false
		);
	}
};

const swimlaneHeaderArrowUp = (
	focusableLane,
	previousSwimlaneHeaderSibling,
	elementsSelector
) => {
	const focusableElementsInLane = querySelectorAllDeep(
		elementsSelector,
		focusableLane,
		false
	);
	// If lane has navigable elements then focus on last elem in lane else focus on the previousSwimlaneHeader
	if (focusableElementsInLane.length) {
		return [focusableElementsInLane[focusableElementsInLane.length - 1]];
	} else {
		return querySelectorAllDeep(
			elementsSelector,
			previousSwimlaneHeaderSibling,
			false
		);
	}
};

const horizontalNavIn2DBoard = (activeElement, elementsSelector, direction) => {
	if (closestDeep(activeElement, SN_LANE)) {
		const currentLane = closestDeep(activeElement, SN_LANE);
		const nextFocusableLane =
			direction === DIRECTIONS.RIGHT
				? currentLane.nextSibling
				: currentLane.previousSibling;
		if (nextFocusableLane)
			return querySelectorAllDeep(
				elementsSelector,
				nextFocusableLane,
				false
			);

		return [];
	} else if (closestDeep(activeElement, SN_LANE_HEADER)) {
		const currentLaneHeader = closestDeep(activeElement, SN_LANE_HEADER);
		const nextFocusableLaneHeader =
			direction === DIRECTIONS.RIGHT
				? currentLaneHeader.nextSibling
				: currentLaneHeader.previousSibling;
		if (nextFocusableLaneHeader)
			return querySelectorAllDeep(
				elementsSelector,
				nextFocusableLaneHeader,
				false
			);

		return [];
	} else if (closestDeep(activeElement, SN_SWIMLANE_HEADER)) {
		return [];
	}
};

const focusLaneFooter = (lane, elementsSelector) => {
	const footerContainer = querySelectorDeep(LANE_FOOTER_CONTAINER, lane);
	if (footerContainer) {
		const elToFocus = querySelectorDeep(elementsSelector, footerContainer);
		if (elToFocus )return [elToFocus];
	}
	return [];
};

/**
 * This is to customize the behavior of 'visual-navigation' module to suit the 2D board component's needs.
 * Few customizations we need are:
 * 1. When pressing Down on a card wrapper, the next card should get focused, even if the lane footer is visually closer
 * 2. When pressing Up on a card wrapper, the previous card should get focused, even if the lane header is visually closer
 * 3. When pressing Right/Left, the immediately next and previous lane's elements get more priority than lanes that can
 * be farther away
 * 4. When on a lane header, pressing Down button should take you to the first swimlane header.
 * 5. When on a swimlane header, pressing Down arrow should take you to next swimlane header if the current swimlane
 * header is collapsed. If expanded, the focus should be on the first element of the lane with the same lane-id as that
 * of the last focused lane header or last focused lane.
 * 6. Same goes for when UP arrow is pressed on a swimlane header just that the focus goes upward.
 */
const getNavigableElementsByDirectionIn2DBoard = (
	activeElement,
	direction,
	swimlaneHeaders,
	elementsSelector,
	board
) => {
	const laneHeaders = querySelectorAllDeep(SN_LANE_HEADER, board, false);

	let swimlaneHeader, laneHeader, lanes, lane;
	// Just pre populating the swimlaneHeader for further use.
	swimlaneHeader = getSwimlaneHeader(activeElement, swimlaneHeaders);

	switch (direction) {
		case DIRECTIONS.DOWN:
			if (closestDeep(activeElement, SN_LANE_HEADER)) {
				// Next element will certainly be the first swimlaneHeader if the activeElement is a lane header
				setLastFocusedLaneItemId(activeElement);
				return {
					ignoreDirection: true,
					element: querySelectorDeep(
						elementsSelector,
						swimlaneHeader
					),
				};
			} else if (closestDeep(activeElement, SN_SWIMLANE_HEADER)) {
				const nextSwimlaneHeader = getNextSwimlaneHeader(
					swimlaneHeader
				);
				if (swimlaneHeader.collapsed) {
					// If the swimlane is collapsed, focus should go onto the next swimlaneHeader.
					return nextSwimlaneHeader
						? [
								querySelectorDeep(
									elementsSelector,
									nextSwimlaneHeader
								),
						  ]
						: [];
				} else {
					const swimlane = closestDeep(activeElement, SN_SWIMLANE);
					lanes = querySelectorAllDeep(SN_LANE, swimlane, false);
					// If lastFocusedLaneItemId is not null then focus on the lane matching the ID else focus on the
					// first lane.
					if (lastFocusedLaneItemId) {
						lane = lanes.find(
							(lane) => lane.lane.id === lastFocusedLaneItemId
						);
						return swimlaneHeaderArrowDown(
							lane,
							nextSwimlaneHeader,
							elementsSelector
						);
					} else {
						return swimlaneHeaderArrowDown(
							lanes[0],
							nextSwimlaneHeader,
							elementsSelector
						);
					}
				}
			} else if (closestDeep(activeElement, SN_LANE)) {
				lane = closestDeep(activeElement, SN_LANE);
				if (
					activeElement.matches(LANE_FOOTER_CONTAINER) ||
					closestDeep(activeElement, LANE_FOOTER_CONTAINER)
				) {
					// Since lane footer is last focusable element in a lane, next element should be either a
					// swimlaneHeader or a null depending on whether the current swimlane is last in index.
					setLastFocusedLaneItemId(activeElement);
					const nextSwimlaneHeader = getNextSwimlaneHeader(
						swimlaneHeader
					);
					return nextSwimlaneHeader
						? [
								querySelectorDeep(
									elementsSelector,
									nextSwimlaneHeader
								),
						  ]
						: [];
				} else if (closestDeep(activeElement, SN_CARD_WRAPPER)) {
					// Return the next card wrapper or the lane footer.
					const cardWrapper = closestDeep(
						activeElement,
						SN_CARD_WRAPPER
					);
					const nextCardWrapper = cardWrapper.nextSibling;
					if (nextCardWrapper) {
						const nextCard = querySelectorDeep(
							elementsSelector,
							nextCardWrapper
						);
						if (nextCard) return [nextCard];
					} else {
						// reached the last card probably, now focus on the  footer
						const footerContainer = querySelectorDeep(
							LANE_FOOTER_CONTAINER,
							lane
						);
						if (footerContainer) {
							return [
								querySelectorDeep(
									elementsSelector,
									footerContainer
								),
							];
						}
					}
				}
			}
			break;
		case DIRECTIONS.UP:
			if (closestDeep(activeElement, SN_SWIMLANE_HEADER)) {
				if (swimlaneHeader === swimlaneHeaders[0]) {
					// If current swimlane is the first in index, then the element above is certainly a lane header.
					// If lastFocusedLaneItemId is not null then focus on the laneHeader matching lastFocusedLaneItemId
					// else focus on first lane header.
					if (lastFocusedLaneItemId) {
						laneHeader = laneHeaders.find(
							(header) => header.id === lastFocusedLaneItemId
						);
						return [
							querySelectorDeep(elementsSelector, laneHeader),
						];
					} else {
						return [
							querySelectorDeep(elementsSelector, laneHeaders[0]),
						];
					}
				} else {
					const previousSwimlaneHeaderSibling = getPreviousSwimlaneHeader(
						swimlaneHeader
					);
					if (previousSwimlaneHeaderSibling.collapsed) {
						return [
							querySelectorDeep(
								elementsSelector,
								previousSwimlaneHeaderSibling
							),
						];
					} else {
						const swimlane = closestDeep(
							previousSwimlaneHeaderSibling,
							SN_SWIMLANE
						);
						lanes = querySelectorAllDeep(SN_LANE, swimlane, false);
						// If lastFocusedLaneItemId is not null then focus on the lane matching the ID else focus on the
						// first lane in the previousSwimlane.
						if (lastFocusedLaneItemId) {
							lane = lanes.find(
								(laneEl) =>
									laneEl.lane.id === lastFocusedLaneItemId
							);
							return swimlaneHeaderArrowUp(
								lane,
								previousSwimlaneHeaderSibling,
								elementsSelector
							);
						} else {
							return swimlaneHeaderArrowUp(
								lanes[0],
								previousSwimlaneHeaderSibling,
								elementsSelector
							);
						}
					}
				}
			} else if (
				activeElement.matches(LANE_FOOTER_CONTAINER) ||
				closestDeep(activeElement, LANE_FOOTER_CONTAINER)
			) {
				// Focus on the last card in the lane, else on swimlaneHeader
				lane = closestDeep(activeElement, SN_LANE);
				const focusableElementsInLane = querySelectorAllDeep(
					elementsSelector,
					lane,
					false
				);
				if (focusableElementsInLane.length > 1) {
					return focusableElementsInLane;
				} else {
					setLastFocusedLaneItemId(activeElement);
					return [
						querySelectorDeep(elementsSelector, swimlaneHeader),
					];
				}
			} else if (closestDeep(activeElement, SN_CARD_WRAPPER)) {
				setLastFocusedLaneItemId(activeElement);
				const cardWrapper = closestDeep(activeElement, SN_CARD_WRAPPER);
				if (cardWrapper.previousSibling) {
					return [
						querySelectorDeep(
							elementsSelector,
							cardWrapper.previousSibling
						),
					];
				}
				return [querySelectorDeep(elementsSelector, swimlaneHeader)];
			} else if (closestDeep(activeElement, SN_LANE_HEADER)) {
				return [];
			}
			break;
		case DIRECTIONS.RIGHT:
		case DIRECTIONS.LEFT:
			return horizontalNavIn2DBoard(
				activeElement,
				elementsSelector,
				direction
			);
	}
	// IDK if nothing works, return everything :3 ?
	return querySelectorAllDeep(elementsSelector, board, false);
};

const getNavigableElementsByDirectionIn1DBoard = (
	activeElement,
	direction,
	elementsSelector,
	board
) => {
	const laneHeaders = querySelectorAllDeep(SN_LANE_HEADER, board, false);
	const lanes = querySelectorAllDeep(SN_LANE, board, false);
	let laneHeader = closestDeep(activeElement, SN_LANE_HEADER);
	const lane =
		closestDeep(activeElement, SN_LANE) ||
		lanes.filter((lane) => lane.id === 'lane_' + laneHeader.id)[0];

	switch (direction) {
		case DIRECTIONS.RIGHT: {
			// Pick the right lane and return all cards from zzzit
			let nextElem = null;
			if (laneHeader) {
				nextElem = laneHeader.nextSibling;
			} else {
				nextElem = lane.nextSibling;
			}
			if (nextElem) {
				return querySelectorAllDeep(elementsSelector, nextElem, false);
			}
			// const nextLane = lane.nextSibling; // TODO: Few cases to be considered here
			// if (nextLane) {
			// 	// TODO: Consider if there are no elements in next lane, what to do?
			// 	return querySelectorAllDeep(elementsSelector, nextLane);
			// }
			return [];
		}
		case DIRECTIONS.LEFT: {
			// Pick the left lane and return all cards from it
			let prevElem = null;
			if (laneHeader) {
				prevElem = laneHeader.previousSibling;
			} else {
				prevElem = lane.previousSibling;
			}
			if (prevElem) {
				return querySelectorAllDeep(elementsSelector, prevElem, false);
			}
			return [];
		}
		case DIRECTIONS.DOWN: {
			// First focus on the next card, and if bottom is reached, then focus on the lane footer
			if (
				activeElement.matches(LANE_FOOTER_CONTAINER) ||
				closestDeep(activeElement, LANE_FOOTER_CONTAINER)
			) {
				return []; // can't go down from lane footer
			}
			if (activeElement.matches(SN_CARD_WRAPPER)) {
				if (activeElement.nextSibling) {
					return [activeElement.nextSibling];
				}
				// If no more cards, focus on lane footer
				return focusLaneFooter(lane, elementsSelector);
			} else if (closestDeep(activeElement, SN_CARD_WRAPPER)) {
				// If we are focussed on an element somewhere inside the card wrapper
				const cardWrapper = closestDeep(activeElement, SN_CARD_WRAPPER);
				const nextCardWrapper = cardWrapper.nextSibling;
				if (nextCardWrapper) {
					const nextCard = querySelectorDeep(
						elementsSelector,
						nextCardWrapper
					);
					if (nextCard) return [nextCard];
				} else {
					// reached the last card probably, now focus on the  footer
					return focusLaneFooter(lane, elementsSelector);
				}
			}
			if (
				activeElement.matches(LANE_HEADER) ||
				closestDeep(activeElement, SN_LANE_HEADER)
			) {
				// Focus on the first card in the lane, otherwise on the lane footer
				const navigableElemsInLane = querySelectorAllDeep(
					elementsSelector,
					lane,
					false
				);
				if (navigableElemsInLane.length) {
					return {
						element: navigableElemsInLane[0],
						ignoreDirection: true,
					};
				}
				return [];
			}
			return querySelectorAllDeep(elementsSelector, lane, false);
		}
		case DIRECTIONS.UP: {
			// First focus on the previous card, and if top is reached, then focus on the lane header
			if (
				activeElement.matches(LANE_HEADER) ||
				closestDeep(activeElement, SN_LANE_HEADER)
			) {
				return []; // can't go up from lane header
			}
			if (activeElement.matches(SN_CARD_WRAPPER)) {
				if (activeElement.previousSibling) {
					return [activeElement.previousSibling];
				}
				// If no more cards, focus on lane header
				laneHeader = laneHeaders.find(
					(header) => header.id === lane.lane.id
				);
				return [querySelectorDeep(elementsSelector, laneHeader)];
			} else if (closestDeep(activeElement, SN_CARD_WRAPPER)) {
				const cardWrapper = closestDeep(activeElement, SN_CARD_WRAPPER);
				const previousCardWrapper = cardWrapper.previousSibling;
				if (previousCardWrapper) {
					const previousCard = querySelectorDeep(
						elementsSelector,
						previousCardWrapper
					);
					if (previousCard) return [previousCard];
					return [querySelectorDeep(elementsSelector, lane)];
				} else {
					// Reached the first card, now focus on the lane header
					const headerContainer = laneHeaders.filter(
						(header) => header.id === lane.lane.id
					)[0];
					if (headerContainer) {
						return [
							querySelectorDeep(
								elementsSelector,
								headerContainer
							),
						];
					}
					return [querySelectorDeep(elementsSelector, lane)];
				}
			}
			if (
				activeElement.matches(LANE_FOOTER_CONTAINER) ||
				closestDeep(activeElement, LANE_FOOTER_CONTAINER)
			) {
				// Focus on the first card in the lane, otherwise on the lane footer
				const navigableElemsInLane = querySelectorAllDeep(
					elementsSelector,
					lane,
					false
				);
				if (navigableElemsInLane.length > 1) {
					return navigableElemsInLane;
				} else {
					const headerContainer = laneHeaders.filter(
						(header) => header.id === lane.lane.id
					)[0];
					if (headerContainer) {
						return [
							querySelectorDeep(
								elementsSelector,
								headerContainer
							),
						];
					}
				}
			}
			return querySelectorAllDeep(elementsSelector, lane, false);
		}
	}
};
