import { nowSortable } from '@devsnc/library-vtb-commons';
import { isEqual } from 'lodash';

console.log(nowSortable);

export default {
	behavior: nowSortable,
	options: {
		items: 'sn-card-wrapper',
		dropzone: '.lane-body .list-elem',
		extendedDropzone: 'sn-lane',
		actionPrefix: 'CARD_',
		listOrder: 'vertical',
		cloneAppendTo: '.board-container',
		xScrollParent: '.sn-board',
		yScrollParent: '.sn-lane',
		connectedLists: 'sn-lane',
		keyboardSupport: false,
		cursorChecker: () => '',
		shouldUpdate: (newProps, oldProps) => {
			// Currently, since there is no store for cards and lanes, the global cards array is filtered repeatedly and
			// the result (a new filtered array) is passed to `sn-lane` component. This causes the `sn-lane` to re-render
			// many times, and that re-initializes the Sortable behaviour on it. Temporarily, to prevent this, only
			// update the Sortable behaviour when some props have really changed (by value), and not just by reference.
			return !isEqual(newProps, oldProps);
		},
		itemsFilter: (item) => item.getProperties().draggable,
		condition: (host) => {
			// Don't add sortable behavior if cloneMode is true
			return !host.cloneMode;
		},
		dynamicConfig: (el) => {
			const propsBasedConfig = {}; // This config object will be merged with the other static config above
			const props = el.getProperties();
			const {
				globalCardConfig: {
					drag_placeholder_tag,
					drop_placeholder_tag,
				},
				swimlaneId,
				customScrollOptions,
			} = props;
			propsBasedConfig.tagSourcePlaceholder = drag_placeholder_tag;
			propsBasedConfig.tagDestPlaceholder = drop_placeholder_tag;
			if (swimlaneId) {
				propsBasedConfig.yScrollParent = '.sn-board';
			}
			if (customScrollOptions && customScrollOptions.enableScroll)
				propsBasedConfig.yScrollParent =
					'.sn-lane .simplebar-content-wrapper';

			return propsBasedConfig;
		},
	},
};
