import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-lane-header.scss';
import If from '../shared/if';
import { t } from 'sn-translate';
import { getStyles } from '../../shared/utils';

export default createCustomElement('sn-lane-header', {
	view({ properties }, { dispatch }) {
		const {
			id,
			cards,
			draggable,
			lane,
			tagLaneHeader,
			laneIndex, // for a11y
			lanesCount,
		} = properties;

		return (
			<div
				className={`sn-lane-header ${draggable ? 'draggable' : ''}`}
				aria-owns={`lane_header_${id} ${cards
					.map((c) => c.id)
					.join(' ')} lane_footer_${id}`}
				draggable={draggable}
				aria-label={t(
					'Lane Header, {0} out of {1}: {2}',
					laneIndex + 1,
					lanesCount,
					lane.title
				)}
				role="columnheader"
				style={getStyles(lane, 'sn-lane-header')}
			>
				<div
					className="lane-header-container"
					id={`lane_header_${id}`}
					draggable={draggable}
				>
					<If test={tagLaneHeader}>
						{/*dot required to resolve tag name*/}
						<properties.tagLaneHeader data={lane} {...lane} />
					</If>
				</div>
			</div>
		);
	},
	properties: {
		id: { default: '', reflect: true },
		lane: { default: {} },
		cards: { default: [] },
		tagLaneHeader: { default: '' },
		draggable: { default: true },
		laneIndex: { default: '' },
		lanesCount: { default: '' },
		swimlanes: { default: '' },
		customScrollOptions: { default: '' },
	},
	styles,
});
