import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import If from '../shared/if';
import styles from './lane-clone.scss';
import {
	querySelectorDeep,
	querySelectorAllDeep,
	isClientRectIntersecting
} from '@devsnc/library-vtb-commons';
import tiltBehaviour from '../../behaviors/tilt/tilt';

/**
 * Copy lane scroll value on the clone for 2D board.
 * */
const copyLaneScrollOnCloneFor2D = (clone, scrollHeight) => {
	// Get content till scroll position
	let scrollElem = querySelectorDeep('.sn-board', clone);
	if (scrollElem) scrollElem.scrollTop = scrollHeight;
};

/**
 * Copy board scroll value on the clone for 1D board.
 * */
const copyLaneScrollOnCloneFor1D = (clone, scrollHeight) => {
	// Get scroll element.
	let scrollElem = querySelectorDeep('.sn-lane', clone);
	if (scrollElem) scrollElem.scrollTop = scrollHeight;
};

/**
 * Sets each swimlane container height in clone.
 * */
const setSwimLaneContainerHeightInClone = (clone, el) => {
	let snBoardElem = querySelectorDeep('.sn-board', el);
	let swimlaneContainers = querySelectorAllDeep(
		'.swimlane-body',
		snBoardElem,
		false
	);

	let swimlaneContainersInClone = querySelectorAllDeep(
		'.swimlane-body',
		clone,
		false
	);

	let swimlaneContainerHeight = [];
	if (swimlaneContainersInClone.length > 0) {
		// Read element offset height of each visible swimlane dom element in user Viewport.
		swimlaneContainers.forEach((element) => {
			swimlaneContainerHeight.push(element.offsetHeight);
		});

		// Apply swimlane height from dom on to clone swimlane dom element.
		swimlaneContainers.forEach((elem, index) => {
			swimlaneContainersInClone[index].style.height =
				swimlaneContainerHeight[index] + 'px';
		});
	}
};

/**
 * Return Swimlane dom elements in user Viewport.
 * */
const getSwimlaneContainerInViewport = (boardEl) => {
	let swimlaneContainersInViewport = [];
	let snBoardElem = querySelectorDeep('.sn-board', boardEl);
	let swimlaneContainers = querySelectorAllDeep(
		'.swimlane-body',
		snBoardElem,
		false
	);
	const boardRect = boardEl.getBoundingClientRect();
	swimlaneContainers.forEach((element) => {
		var swimLaneRect = element.getBoundingClientRect();
		if (isClientRectIntersecting(swimLaneRect, boardRect))
			swimlaneContainersInViewport.push(element);
	});

	return swimlaneContainersInViewport;
};

/**
 * Return cards array from swimlane dom element in user Viewport.
 * */
const getCardsInSwimlaneViewport = (laneId, swimlaneContainers) => {
	let cardsInViewport = [];
	swimlaneContainers.forEach((element) => {
		cardsInViewport = cardsInViewport.concat(
			getCardsInDraggedLane(laneId, element)
		);
	});
	return cardsInViewport;
};

/**
 * Return scrolling required on clone for 1D board.
 * */
const getScrollHeightFor1DBoard = (elem, laneId, card) => {
	let lanes = querySelectorAllDeep('sn-lane', elem, false);
	let dragLane = lanes.find((lane) => lane.id.includes(laneId));
	let dragLaneRect = dragLane.getBoundingClientRect();
	let scrollingRequired = '';

	const cardContainer = querySelectorDeep(`[id="${card.id}"]`, dragLane);
	const cardRect = cardContainer.getBoundingClientRect();

	if (isClientRectIntersecting(cardRect, dragLaneRect)) {
		scrollingRequired = dragLaneRect.top - cardRect.top;
	}

	return scrollingRequired;
};

/**
 * Return scrolling required on clone for 2D board.
 * */
const getScrollHeightFor2DBoard = (scrollContainer) =>
	scrollContainer.scrollTop;

/**
 * Return cards array from lane dom element in user Viewport.
 * */
const getCardsInDraggedLane = (laneId, elem) => {
	let cardsInViewport = [];

	let lanes = querySelectorAllDeep('sn-lane', elem, false);
	let dragLane = lanes.find((lane) => lane.id.includes(laneId));
	let cardContainers = querySelectorAllDeep(
		'sn-card-wrapper',
		dragLane,
		false
	);
	let props = dragLane.getProperties();
	let dragLaneRect = dragLane.getBoundingClientRect();
	const { cards } = props;
	cardContainers.forEach((elem, index) => {
		var cardRect = elem.getBoundingClientRect();
		if (isClientRectIntersecting(cardRect, dragLaneRect))
			cardsInViewport.push(cards[index]);
	});
	return cardsInViewport;
};

export default createCustomElement('lane-clone', {
	view(state) {
		const {
			props: {
				id,
				laneHeaderTag,
				laneFooterTag,
				cardConfigOverride,
				laneConfigOverride,
				swimlanes,
				swimlaneHeaderCloneTag,
				lanes,
				cardTag,
				height,
				styles,
			},
		} = state.properties;
		const { cardsInSwimLaneViewport, cardsInLaneViewPort } = state;
		const dragLanes = lanes.filter((lane) => lane.id === id);
		const isSwimlaneBoard = swimlanes ? swimlanes.length > 0 : false;
		return (
			<div
				className="lane-clone"
				style={{
					height: `${height}px`,
				}}
			>
				<If test={isSwimlaneBoard}>
					<now-visual-board
						cards={cardsInSwimLaneViewport}
						swimlanes={swimlanes}
						card-tag={cardTag}
						lanes={dragLanes}
						lane-header-tag={laneHeaderTag}
						card-config-override={cardConfigOverride}
						lane-config-override={laneConfigOverride}
						swimlane-header-tag={swimlaneHeaderCloneTag}
						clone-mode={true}
						styles={styles}
					/>
				</If>
				<If test={!isSwimlaneBoard}>
					<now-visual-board
						cards={cardsInLaneViewPort}
						cardTag={cardTag}
						lanes={dragLanes}
						lane-header-tag={laneHeaderTag}
						lane-footer-tag={laneFooterTag}
						card-config-override={cardConfigOverride}
						lane-config-override={laneConfigOverride}
						clone-mode={true}
						styles={styles}
					/>
				</If>
			</div>
		);
	},
	properties: {
		props: { default: null },
		horizontalDragDirection: { default: '' },
	},
	initialState: {
		swimlanesInViewport: [],
		cardsInSwimLaneViewport: [],
		cardsInLaneViewPort: [],
		scrollHeight: '',
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: tiltBehaviour,
		[actionTypes.COMPONENT_CONNECTED]: ({ host, updateState }) => {
			tiltBehaviour({ host });
			const { props } = host.getProperties();
			const { id, boardEl, swimlanes } = props;
			const isSwimlaneBoard = swimlanes ? swimlanes.length > 0 : false;

			if (isSwimlaneBoard) {
				// Get Swimlane container from user Viewport on current board.
				const swimlaneContainers = getSwimlaneContainerInViewport(
					boardEl
				);

				// Get cards from swimlane container which are in user vieport on current board.
				const getCardsInSwimlane = getCardsInSwimlaneViewport(
					id,
					swimlaneContainers
				);
				let scrollContainer = querySelectorDeep('.sn-board', boardEl);
				const scrollHeight = getScrollHeightFor2DBoard(scrollContainer);

				updateState({
					swimlanesInViewport: swimlaneContainers,
					cardsInSwimLaneViewport: getCardsInSwimlane,
					scrollHeight: scrollHeight,
				});
			} else {
				// Get cards from lane which is being dragged.
				const cardsInLane = getCardsInDraggedLane(id, boardEl);
				let scrollingRequired = '';
				updateState({
					cardsInLaneViewPort: cardsInLane,
				});
				if (cardsInLane.length > 0) {
					scrollingRequired = getScrollHeightFor1DBoard(
						boardEl,
						id,
						cardsInLane[0]
					);
				}

				updateState({
					scrollHeight: scrollingRequired,
				});
			}
		},
		[actionTypes.COMPONENT_RENDERED]: ({ host, state }) => {
			const { props } = host.getProperties();
			const { swimlanes, boardEl } = props;
			const isSwimlaneBoard = swimlanes ? swimlanes.length > 0 : false;
			const { scrollHeight } = state;

			if (isSwimlaneBoard) {
				// Get scroll container for current board.
				let scrollContainer = querySelectorDeep('.sn-board', boardEl);

				// Set Swimlane container height for each lane in swimlane in clone.
				setSwimLaneContainerHeightInClone(host, scrollContainer);

				// Copy board scroll height on clone element.
				setTimeout(() => {
					copyLaneScrollOnCloneFor2D(host, scrollHeight);
				}, 100);
			} else {
				const { scrollHeight } = state;
				// Copy lane scroll height on clone element.
				setTimeout(() => {
					copyLaneScrollOnCloneFor1D(host, scrollHeight);
				}, 500);
			}
		},
	},
	styles,
});
