import { nowSortable } from '@devsnc/library-vtb-commons';
import { isEqual } from 'lodash';
import { closestDeep } from '@devsnc/library-vtb-commons';

const SL_FIRT_LANE_SCROLL_BUFFER_AREA = 25;

export default {
	behavior: nowSortable,
	options: {
		items: 'sn-swimlane',
		dropzone: '.swimlanes',
		actionPrefix: 'SWIMLANE_',
		allowFrom: '.sn-swimlane-header',
		cloneAppendTo: '.board-container',
		xScrollParent: '.sn-board',
		yScrollParent: '.sn-board',
		listOrder: 'vertical',
		lockAxis: 'y',
		keyboardSupport: false,
		cursorChecker: () => '',
		clone: (el) => {
			const properties = el.getProperties();
			const cloneTag =
				properties.tagSwimlaneHeader || 'sn-swimlane-header';
			let cloneEl = document.createElement(cloneTag);
			for (let prop in properties) cloneEl[prop] = properties[prop];
			cloneEl.data = properties.swimlane;
			cloneEl.collapsed = true;
			cloneEl.clone = true;
			cloneEl.style.width = `${el.getBoundingClientRect().width}px`;
			return cloneEl;
		},
		shouldUpdate: (newProps, oldProps) => {
			return !isEqual(newProps, oldProps); // see "card-sort.js" for an explanation of this
		},
		onSrcPlaceholderInsert: (dragElem, srcPlaceholder) => {
			srcPlaceholder.style.top = dragElem.style.top;
			srcPlaceholder.style.position = 'absolute';
		},
		onDestPlaceholderInsert: (dragElem, srcPlaceholder) => {
			// Expectation : source placeholder and destination placeholder
			// should be of same height as collapsed swimlane height.
			// Also, srcPlaceholder.height cannot be used as it can be full swimlane height if it is dragged before collapse
			const {
				height: collapsedSlHeight,
			} = srcPlaceholder.getBoundingClientRect();
			// if srcPlaceholder is not yet rendered (height zero), we do not need any css adjustments
			if (collapsedSlHeight === 0) return;

			// Get all swimlanes and calculate the first el top w.r.t source placeholder top
			const slContainerEl = closestDeep(dragElem, '.swimlanes');
			const swimlaneElements = [].slice.call(slContainerEl.children); // including placeholders
			const dragElIndex = swimlaneElements.indexOf(dragElem);
			const placeholderTop = parseInt(srcPlaceholder.style.top) || 0;
			let firstlaneTop = placeholderTop - dragElIndex * collapsedSlHeight;
			if (firstlaneTop < 0) {
				firstlaneTop +=
					collapsedSlHeight + SL_FIRT_LANE_SCROLL_BUFFER_AREA;
			}

			// Update 'top' of all swimlane elements including src and dest placeholders
			swimlaneElements.splice(dragElIndex, 1);
			swimlaneElements.forEach((sl) => {
				sl.style.top = `${firstlaneTop}px`;
				sl.style.position = 'absolute';
				firstlaneTop += collapsedSlHeight;
			});
		},
		condition: (host) => {
			// Don't add sortable behavior if cloneMode is true
			return !host.cloneMode;
		},
		itemsFilter: (el) => el.getProperties().draggable,
		dynamicConfig: (el) => {
			const consumerConfig = {};
			const props = el.getProperties();
			const {
				swimlaneDragPlaceholderTag,
				swimlaneDropPlaceholderTag,
			} = props;
			if (swimlaneDragPlaceholderTag)
				consumerConfig.tagSourcePlaceholder = swimlaneDragPlaceholderTag;
			if (swimlaneDropPlaceholderTag)
				consumerConfig.tagDestPlaceholder = swimlaneDropPlaceholderTag;
			return consumerConfig;
		},
	},
};
