import { createCustomElement } from '@servicenow/ui-core';
import './card-move-modal/card-move-modal';
import './lane-move-modal/lane-move-modal';

export default createCustomElement('board-accessibility', {
	view(state) {
		const {
			properties: {
				cards,
				lanes,
				options,
				laneConfigOverride,
				swimlanes,
				swimlaneConfigOverride,
				customScrollOptions,
			},
		} = state;

		if (options.action === 'CARD_MOVE') {
			return (
				<card-move-modal
					lanes={lanes}
					cards={cards}
					selected-card={options.cardId}
					lane-config-override={laneConfigOverride}
					swimlane-config-override={swimlaneConfigOverride}
					swimlanes={swimlanes}
				/>
			);
		} else if (options.action === 'LANE_MOVE') {
			return (
				<lane-move-modal
					lanes={lanes}
					selected-lane={options.laneId}
					lane-type="vertical"
					custom-scroll-options={customScrollOptions}
					lane-config-override={laneConfigOverride}
				/>
			);
		} else if (options.action === 'SWIM_LANE_MOVE') {
			return (
				<lane-move-modal
					lanes={swimlanes}
					selected-lane={options.laneId}
					lane-type="swimlane"
					lane-config-override={swimlaneConfigOverride}
				/>
			);
		}
	},
	properties: {
		cards: { default: {} },
		lanes: { default: {} },
		options: { default: {} },
		laneConfigOverride: { default: {} },
		swimlanes: { default: {} },
		swimlaneConfigOverride: { default: {} },
		customScrollOptions: { default: '' },
	},
	actionHandlers: {
		'NOW_VISUAL_BOARD#A11Y#OPTIONS_RESET': ({ updateProperties }) =>
			updateProperties({ options: {} }),
	},
});
