import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import keyBindingBehavior from '@servicenow/behavior-key-binding';
import styles from './rearrange-list.scss';
import { REARRANGE_LIST_INSTRUCTIONS } from '../../../constants/translated';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';

const swapElements = function (array, indexA, indexB) {
	const temp = array[indexA];
	array[indexA] = array[indexB];
	array[indexB] = temp;
};

const getUpdatedItemsList = (items, direction) => {
	let currentIndex = items.findIndex((item) => item.selected);
	if (direction === 'ArrowDown') {
		if (currentIndex + 1 < items.length) {
			swapElements(items, currentIndex, ++currentIndex);
		}
	} else if (direction === 'ArrowUp') {
		if (currentIndex - 1 >= 0) {
			swapElements(items, currentIndex, --currentIndex);
		}
	}

	return { updatedIndex: currentIndex, updatedItems: items };
};

export default createCustomElement('rearrange-list', {
	view(state) {
		const {
			properties: { configAria, items },
		} = state;
		return (
			<div>
				<p
					id="rearrange_instructions"
					className="sr-only"
					aria-hidden="true"
				>
					{REARRANGE_LIST_INSTRUCTIONS}
				</p>
				<ul
					className="items-container"
					aria-label={configAria['aria-label']}
					aria-describedby="rearrange_instructions"
				>
					{items.map((item, index) => {
						return item.selected === true ? (
							<li
								tabIndex="0"
								className="active"
								role="listitem"
								aria-label={
									'Selected item in position ' +
									(index + 1) +
									', out of ' +
									items.length +
									' items'
								}
							>
								<span className="item-text">
									{++index}. {item.displayValue}
								</span>
								{/* <span className="grippy"></span> */}
							</li>
						) : (
							<li role="listitem" key={`listitem${index}`}>
								<span className="item-text">
									{++index}. {item.displayValue}
								</span>
							</li>
						);
					})}
				</ul>
			</div>
		);
	},
	properties: {
		items: { default: [] },
		configAria: { default: {} },
		focusActiveItem: { default: false },
	},
	behaviors: [
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					onMove: ['ArrowUp', 'ArrowDown'],
				},
				handlers: {
					onMove: (host, event, dispatch) => {
						event.stopPropagation();
						event.preventDefault();
						let direction =
							event.keyCode === 38 ? 'ArrowUp' : 'ArrowDown';
						dispatch('CHANGE_ORDER', { direction, host });
					},
				},
			},
		},
	],
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ host, properties }) => {
			const activeEl = querySelectorDeep('li.active', host);
			if (activeEl) {
				setTimeout(() => {
					activeEl.scrollIntoView({ block: 'center' });
					if (properties.focusActiveItem) activeEl.focus();
				}, 0);
			}
		},
		['ORDER_CHANGED']: ({ action }) => {
			const {
				payload: { host },
			} = action;
			setTimeout(() => {
				const activeEl = querySelectorDeep('li.active', host);
				if (activeEl) activeEl.focus();
			}, 50);
		},
		['CHANGE_ORDER']: ({ state, updateProperties, action, dispatch }) => {
			const {
				properties: { items },
			} = state;
			const {
				payload: { direction, host },
			} = action;
			const { updatedIndex, updatedItems } = getUpdatedItemsList(
				items,
				direction
			);
			updateProperties({ items: [...updatedItems] });
			dispatch('ORDER_CHANGED', { host });
			dispatch('NOW_VISUAL_BOARD#REARRANGE_LIST#ORDER_CHANGED', {
				updatedItems,
				updatedIndex,
			});
		},
	},
	styles,
});
