import { actionTypes } from '@servicenow/ui-core';
import swimlaneCollapse from './swimlane-collapse';

export default {
	name: 'swimlane-collapse',
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ host, properties }) => {
			if (properties.draggable) swimlaneCollapse(host).addBehaviour();
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({ action, host }) => {
			const { name } = action.payload;
			if (name === 'draggable') {
				const {
					previousValue: previouslyDraggable,
					value: nowDraggable,
				} = action.payload;
				
				if (!previouslyDraggable && nowDraggable)
					swimlaneCollapse(host).addBehaviour();
				else if (previouslyDraggable && !nowDraggable)
					swimlaneCollapse(host).removeBehaviour();
			}
		},
	},
};
