import { visualNavigation } from '@devsnc/library-vtb-commons';
import {
	BOARD_ARROW_NAVIGABLE_ELEMENTS,
	getNavigableElementsByDirection,
} from './visual-nav-override';
import get from 'lodash/get';

export const boardNavigationBehavior = {
	behavior: visualNavigation,
	options: {
		name: 'BoardNav',
		elements: BOARD_ARROW_NAVIGABLE_ELEMENTS,
		// To Do : Need to handle corner scenarios in visual-nav-overrides.js
		targetElements: getNavigableElementsByDirection(
			BOARD_ARROW_NAVIGABLE_ELEMENTS
		),
		canTabToElements: false,
		initDelay: 100,
		dynamicConfig: (coeffects) => {
			// This will be merged with the static config
			const config = {};
			const { properties } = coeffects;
			const elements = get(properties, 'arrowNavigableElements');
			const makeElementsTabbable = get(
				properties,
				'makeElementsTabbable',
				false
			);
			// Consumers of board component can choose if they want the arrow nav behavior
			const enable = get(properties, 'useArrowNavigation');
			if (!enable) config.disable = true;

			if (elements) {
				// elements to focus on might change based on the props (VTB vs Playbook)
				config.elements = elements;
				config.targetElements = getNavigableElementsByDirection(
					elements
				);
				config.canTabToElements = makeElementsTabbable;
			}

			return config;
		},
	},
};
