import {
	ACTION_PREFIX,
	CARD_MOVED,
	LANE_MOVED,
	SWIMLANE_MOVED,
	CARD_SORT_END,
	LANE_SORT_END,
	SWIMLANE_SORT_END,
} from '../constants/actions';
import { closestDeep } from '@devsnc/library-vtb-commons';
import { removeVLanePlaceholders } from '../components/sn-lane/dragHelper';
import { expandSwimlanes } from '../behaviors/swimlane-collapse/collapse';

// Listen to events dispatch from interactjs based sortable library, and dispatch board-specific events
export default {
	[CARD_SORT_END]: ({ action, dispatch }) => {
		const {
			element,
			sourceParent,
			sourceIndex,
			destinationParent,
			destinationIndex,
		} = action.payload;

		const toLaneEl = closestDeep(destinationParent, 'sn-lane');
		const fromLaneEl = closestDeep(sourceParent, 'sn-lane');

		//TODO : Change lane id to plain sys_id, instead of lane_sysid and remove below
		const toLaneId = toLaneEl ? toLaneEl.id.split('_')[1] : null;
		const fromLaneId = fromLaneEl ? fromLaneEl.id.split('_')[1] : null;

		const cardMovePayload = {
			id: element.id,
			fromLane: fromLaneId,
			fromPosition: sourceIndex,
			toLane: toLaneId,
			toPosition: destinationIndex,
			fromSwimlane: fromLaneEl.swimlaneId,
			toSwimlane: toLaneEl.swimlaneId,
		};

		dispatch(ACTION_PREFIX + CARD_MOVED, cardMovePayload);
	},
	[LANE_SORT_END]: ({ action, dispatch }) => {
		removeVLanePlaceholders();
		const { element, sourceIndex, destinationIndex } = action.payload;
		dispatch(ACTION_PREFIX + LANE_MOVED, {
			id: element.id,
			fromPosition: sourceIndex,
			toPosition: destinationIndex,
		});
	},
	[SWIMLANE_SORT_END]: ({ action, dispatch }) => {
		const { element, sourceIndex, sourceParent, destinationIndex } = action.payload;
		expandSwimlanes(sourceParent);
		dispatch(ACTION_PREFIX + SWIMLANE_MOVED, {
			id: element.id,
			fromPosition: sourceIndex,
			toPosition: destinationIndex,
		});
	},
};
