import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-modal';
import '../../shared/rearrange-list/rearrange-list';
import styles from './lane-move-modal.scss';
import {
	ACTION_PREFIX,
	LANE_MOVED,
	SWIMLANE_MOVED,
} from '../../../constants/actions';
import {
	MOVE_LANE,
	CANCEL,
	ARRANGE_LANE_ORDER,
	CURRENT,
	ARRANGE_SWIMLANE_ORDER,
	MOVE_SWIMLANE,
} from '../../../constants/translated';

const isVisibleInReorder = (laneId, laneConfigOverride) => {
	return laneConfigOverride && laneConfigOverride[laneId]
		? laneConfigOverride[laneId]['hide_in_reorder'] !== true
		: true;
};

const getLaneMoveOptions = (lanes, currentLaneId, laneConfigOverride) => {
	if (!currentLaneId) return lanes;
	return lanes
		.filter((lane) => {
			return (
				isVisibleInReorder(lane.id, laneConfigOverride) ||
				lane.id === currentLaneId
			);
		})
		.map((lane) => {
			const laneTitle =
				lane.title + (lane.id === currentLaneId ? ` (${CURRENT})` : '');
			return {
				displayValue: laneTitle,
				value: lane.id,
				selected: lane.id === currentLaneId,
			};
		});
};

export default createCustomElement('lane-move-modal', {
	view(state) {
		const {
			properties: {
				lanes,
				selectedLane,
				laneType,
				customScrollOptions,
				laneConfigOverride,
			},
			laneMoveOptions,
		} = state;

		const currentLane = lanes.find((lane) => lane.id === selectedLane);
		if (!currentLane) return;

		const currentLaneOptions =
			laneMoveOptions ||
			getLaneMoveOptions(lanes, selectedLane, laneConfigOverride);
		const modalTitle = `${
			laneType === 'vertical' ? MOVE_LANE : MOVE_SWIMLANE
		}: ${currentLane.title}`;
		const modalFooterActions = [
			{
				id: 'move_lane',
				variant: 'primary',
				label: laneType === 'vertical' ? MOVE_LANE : MOVE_SWIMLANE,
			},
			{ id: 'cancel', variant: 'secondary', label: CANCEL },
		];

		return (
			<now-modal
				opened
				size="lg"
				header-label={modalTitle}
				footer-actions={modalFooterActions}
				append-to-payload={customScrollOptions}
			>
				<p className="select-label">
					{laneType === 'vertical'
						? ARRANGE_LANE_ORDER
						: ARRANGE_SWIMLANE_ORDER}
				</p>
				<div className="lanes-rearrange-container">
					<rearrange-list
						config-aria={{
							'aria-label':
								laneType === 'vertical'
									? ARRANGE_LANE_ORDER
									: ARRANGE_SWIMLANE_ORDER,
						}}
						items={currentLaneOptions}
						focus-active-item="true"
					/>
				</div>
			</now-modal>
		);
	},
	initialState: {
		updatedOrder: -1,
		laneMoveOptions: null,
	},
	properties: {
		lanes: { default: {} },
		selectedLane: { schema: { type: 'string' } },
		laneType: { default: '' },
		laneConfigOverride: { default: {} },
		customScrollOptions: { default: '' },
	},
	actionHandlers: {
		'NOW_MODAL#OPENED_SET': ({ dispatch }) =>
			dispatch('NOW_VISUAL_BOARD#A11Y#OPTIONS_RESET'),
		'NOW_MODAL#FOOTER_ACTION_CLICKED': ({
			action,
			state,
			properties,
			dispatch,
		}) => {
			const lanes = properties.lanes;
			const laneId = properties.selectedLane;
			const laneType = properties.laneType;
			const customScrollOptions = properties.customScrollOptions;
			const actionType = action.payload.footerAction.id;

			if (actionType === 'move_lane' && state.updatedOrder >= 0) {
				laneType === 'vertical'
					? dispatch(ACTION_PREFIX + LANE_MOVED, {
							id: laneId,
							fromPosition: lanes.findIndex(
								(lane) => lane.id === laneId
							),
							toPosition: state.updatedOrder,
							eventType: 'keyboard',
							customScrollOptions: customScrollOptions,
					  })
					: dispatch(ACTION_PREFIX + SWIMLANE_MOVED, {
							id: laneId,
							fromPosition: lanes.findIndex(
								(lane) => lane.id === laneId
							),
							toPosition: state.updatedOrder,
							eventType: 'keyboard',
					  });
			}
			dispatch('NOW_VISUAL_BOARD#A11Y#OPTIONS_RESET');
		},
		'NOW_VISUAL_BOARD#REARRANGE_LIST#ORDER_CHANGED': ({
			action,
			updateState,
		}) => {
			updateState({
				updatedOrder: action.payload.updatedIndex,
				laneMoveOptions: action.payload.updatedItems,
			});
		},
	},
	styles,
});
