import { createCustomElement } from '@servicenow/ui-core';
import '../accessibility/board-accessibility';
import { view } from './board-view';
import styles from './now-visual-board.scss';
import dndActionHandlers from '../../actions/dnd-handlers';
import a11yActionHandlers from '../../actions/a11y-modal-handlers';
import uxfImportHandlers from '../../actions/uxf-import-handlers';
import { boardNavigationBehavior } from '../../accessibility/board-arrow-navigation';
import laneSortBehavior from '../../behaviors/sortable/lane-sort';
import swimlaneSortBehavior from '../../behaviors/sortable/swimlane-sort';
import { REGISTER_WRAPPER_EVENT } from '../../constants/actions';

/**
 * Main Board component that renders lanes and cards
 *
 * Usage:
 *
 * <now-visual-board
 * 		cards={[
 * 			{ id: "card_1", title: "Email server is down", order: 1 }
 * 			{ id: "card_2", title: "Network is slow", order: 3 }
 * 			{ id: "card_3", title: "Server space full", order: 2 }
 * 		]}
 * 		lanes={[
 * 			{ id: "lane_1", title: "In Progress" },
 * 			{ id: "lane_2", title: "Completed" }
 * 		]}
 * 		card-tag="sn-incident-card"
 * 		lane-header-tag="vtb-lane-header"
 * 		lane-footer-tag="vtb-lane-footer"
 * 		use-arrow-navigation="true"
 * 		arrow-navigable-elements=".lane-header,.incident-card,.add-card-btn"
 * />
 *
 * */
export default createCustomElement('now-visual-board', {
	view,
	initialState: {
		a11yOptions: {},
	},
	properties: {
		/**
		 * Array of cards data to be rendered in the card slot
		 * @type {Array}
		 */
		cards: {
			required: true,
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						id: { type: 'string' },
						title: { type: 'string' },
						order: { type: 'number' },
						lane_id: { type: 'string' },
					},
				},
			},
			default: [],
		},

		/**
		 * Array of lanes data
		 * @type {Array}
		 */
		lanes: {
			required: true,
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						id: { type: 'string' },
						title: { type: 'string' },
					},
				},
			},
			default: [],
		},

		/**
		 * Array of swimlanes data
		 * @type {Array}
		 */
		swimlanes: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						id: { type: 'string' },
						title: { type: 'string' },
					},
				},
			},
			default: [],
		},
		/**
		 * Element name to be rendered in the card slot
		 * @type {string}
		 */
		cardTag: { required: true, schema: { type: 'string' }, default: '' },
		/**
		 * Element to be shown as a placeholder for source card when drag event starts
		 * @type {string}
		 */
		cardDragPlaceholderTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Element to be shown as a placeholder for card at the drop zone during the drag event
		 * @type {string}
		 */
		cardDropPlaceholderTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Set this flag to false if you don't want the cards to be draggable
		 * @type {boolean}
		 * @default {true}
		 */
		cardDraggable: { schema: { type: 'boolean' }, default: true },
		/**
		 * Element to be rendered in the lane header slot
		 * @type {string}
		 */
		laneHeaderTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Element to be rendered in the lane footer slot
		 * @type {string}
		 */
		laneFooterTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Element to be shown as a placeholder for source lane when drag event starts
		 * @type {string}
		 */
		laneDragPlaceholderTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Element to be shown as a placeholder for lane at the drop zone during the drag event
		 * @type {string}
		 */
		laneDropPlaceholderTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Set this flag to false if you don't want the lanes to be draggable
		 * @type {boolean}
		 * @default {true}
		 */
		laneDraggable: { schema: { type: 'boolean' }, default: true },
		/**
		 * Element to be rendered in the swimlane header slot
		 * @type {string}
		 */
		swimlaneHeaderTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Element to be rendered in the swimlane header clone slot
		 * @type {string}
		 */
		swimlaneHeaderCloneTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Element to be shown as a placeholder for source swimlane when drag event starts
		 * @type {string}
		 */
		swimlaneDragPlaceholderTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Element to be shown as a placeholder for swimlane at the drop zone during the drag event
		 * @type {string}
		 */
		swimlaneDropPlaceholderTag: { schema: { type: 'string' }, default: '' },
		/**
		 * Set this flag to false if you don't want the swimlanes to be draggable
		 * @type {boolean}
		 * @default {true}
		 */
		swimlaneDraggable: { schema: { type: 'boolean' }, default: true },
		/**
		 * Additional override properties for each lane.
		 * Usage:
		 * 	laneConfigOverride: {
		 * 		'lane-id': {
		 * 			tag : '<string>',
					drag-placeholder-tag : '<string>',
					drop-placeholder-tag : '<string>',
					draggable :'<boolean>',
		 * 		}
		 * 	}
		 * @type {Object}
		 */
		laneConfigOverride: {
			schema: { type: 'object' },
			default: {},
		},
		/**
		 * Additional override properties for each swimlane.
		 * Usage:
		 * 	laneConfigOverride: {
		 * 		'lane-id': {
		 * 			tag : '<string>',
					drag-placeholder-tag : '<string>',
					drop-placeholder-tag : '<string>',
					draggable :'<boolean>',
		 * 		}
		 * 	}
		 * @type {Object}
		 */
		swimlaneConfigOverride: {
			schema: { type: 'object' },
			default: {},
		},
		cloneMode: { schema: { type: 'boolean' }, default: false },
		/**
		 * Additional override properties for each card.
		 * Usage:
		 * 	cardConfigOverride: {
		 * 		'card-id': {
		 * 			tag : '<string>',
					drag-placeholder-tag : '<string>',
					drop-placeholder-tag : '<string>',
					draggable :'<boolean>',
		 * 		}
		 * 	}
		 * @type {Object}
		 */
		cardConfigOverride: {
			schema: { type: 'object' },
			default: {},
		},
		/**
		 * This flag allow the board to be accessible using arrow keys.
		 * Arrow navigation by default works on the wrapper elements of the board.
		 * To override this behavior use @property {arrowNavigableElements}
		 * @type {boolean}
		 */
		useArrowNavigation: { schema: { type: 'boolean' }, default: true },
		/**
		 * Comma separated values of selectors for arrow navigation
		 * @type {string}
		 */
		arrowNavigableElements: { schema: { type: 'string' }, default: '' },
		/**
		 * Actions that are dispatched for native DOM events on wrapper elements
		 * Supported Wrapper elements- ['sn-lane-header', 'sn-swimlane-header', 'sn-lane', 'sn-card-wrapper', 'lane-footer-container']
		 * @type {Array}
		 * @property events -List of Native DOM event names
		 * @property action -Action dispatched for the corresponding DOM event
		 * @property on -List of wrapper container names (whitelisted above)
		 */
		wrapperDomEvents: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						on: { type: 'array' },
						events: { type: 'array' },
						action: { type: 'string' },
					},
					additionalProperties: false,
				},
			},
			default: [],
		},
		/**
		 * Custom scrollBar options for lane
		 * @property scrollTrack styles to be applied to scroll track
		 * @property scrollbar styles to be applied to scroll bar
		 * Example: customScrollOptions : {
		 * 				enableScroll: true,
		 * 				scrollTrackCSS: {
		 * 					background: 'yellow',
		 * 					height: '80%',
		 * 					..other styles
		 * 				},
		 * 				scrollBarCSS: {
		 * 					color: 'blue',
		 *					..other styles
		 * 				}
		 * 			}
		 *
		 */
		customScrollOptions: {
			schema: {
				type: 'object',
				properties: {
					enableScroll: { type: 'boolean', default: false },
					scrollTrackCSS: { type: 'object' },
					scrollBarCSS: { type: 'object' },
				},
			},
		},
		/**
		 * A boolean indicating whether the board should fetch the components mentioned by the tags, from
		 * UXF registry and load them in the browser. The component should be available in the UXF registry
		 * for this to work.
		 *
		 * Example: If the property `card-tag` is passed as `"sn-component-vtb-card"`, then the board will fetch this
		 * component from UXF registry and it need not be manually imported in the code by the board consumer.
		 *
		 * */
		loadTagsFromUxf: {
			default: false,
			schema: {
				type: 'boolean',
			},
		},
		styles: {
			default: {},
			schema: {
				type: 'object',
			},
		},
	},
	dispatches: {
		/**
		 * Dispatched when card drag has ended and has been dropped in a location
		 * @type {{id:string, fromLane:string, toLane:string, fromSwimlane:string, toSwimlane:string,
		 * fromPosition:number, toPosition:number}}
		 */
		'NOW_VISUAL_BOARD#CARD_MOVED': {},
		/**
		 * Dispatched when lane drag has ended
		 * @type {{id:string, fromPosition:number, toPosition:number}}
		 */
		'NOW_VISUAL_BOARD#LANE_MOVED': {},
		/**
		 * Dispatched when swimlane drag has ended
		 * @type {{id:string, fromPosition:number, toPosition:number}}
		 */
		'NOW_VISUAL_BOARD#SWIMLANE_MOVED': {},
	},
	actionHandlers: {
		...dndActionHandlers,
		...a11yActionHandlers,
    ...uxfImportHandlers,
		[REGISTER_WRAPPER_EVENT]: ({ state, action, dispatch }) => {
			const {
				payload: { element, name },
			} = action;
			const {
				properties: { wrapperDomEvents },
			} = state;
			wrapperDomEvents.forEach(
				({ events = [], action = '', on = [], capture = false }) => {
					if (on.indexOf(name) >= 0) {
						events.forEach((eventName) => {
							element.addEventListener(
								eventName,
								(event) => {
									dispatch(action, { event, name });
								},
								capture
							);
						});
					}
				}
			);
		},
	},
	styles,
	behaviors: [
		boardNavigationBehavior,
		laneSortBehavior,
		swimlaneSortBehavior,
	],
});
