import '../sn-swimlane/sn-swimlane';
import '../sn-swimlane-header/sn-swimlane-header';
import '../sn-lane/sn-lane';
import '../sn-lane-header/sn-lane-header';
import '../accessibility/board-accessibility';
import If from '../shared/if';
import get from 'lodash/get';
import { getCardsForLane, getCardsForSwimlane } from '../../shared/utils';
import { REGISTER_WRAPPER_EVENT } from '../../constants/actions';

export const view = (state, { dispatch }) => {
	const {
		properties: {
			cards,
			lanes,
			swimlanes,
			cardTag: globalCardTag,
			cardDraggable: globalCardDraggable,
			cardDragPlaceholderTag: globalCardDragPlaceholderTag,
			cardDropPlaceholderTag: globalCardDropPlaceholderTag,
			laneHeaderTag: globalLaneHeaderTag,
			laneFooterTag: globalLaneFooterTag,
			laneDragPlaceholderTag: globalLaneDragPlaceholderTag,
			laneDropPlaceholderTag: globalLaneDropPlaceholderTag,
			laneDraggable: globalLaneDraggable,
			swimlaneHeaderTag: globalSwimlaneHeaderTag,
			swimlaneDragPlaceholderTag: globalSwimlaneDragPlaceholderTag,
			swimlaneDropPlaceholderTag: globalSwimlaneDropPlaceholderTag,
			swimlaneDraggable: globalSwimlaneDraggable,
			swimlaneConfigOverride,
			laneConfigOverride,
			cardConfigOverride,
			wrapperDomEvents,
			cloneMode,
			styles,
			customScrollOptions,
		},
		a11yOptions,
	} = state;

	const globalCardConfig = {
		tag: globalCardTag,
		draggable: globalCardDraggable,
		drag_placeholder_tag: globalCardDragPlaceholderTag,
		drop_placeholder_tag: globalCardDropPlaceholderTag,
	};

	const IS_SWIMLANE_BOARD = swimlanes && swimlanes.length > 0;

	return (
		<div className="board-container" role="grid">
			<div
				className={`sn-board ${
					IS_SWIMLANE_BOARD ? 'swimlane-board' : ''
				}`}
				style={get(styles, 'sn-board', '')}
			>
				{/***********************
				 ****** Lane Header *****
				 ***********************/}
				<header className="lanes-header" role="rowgroup">
					<div
						className="lanes-header-container"
						style={get(styles, 'lanes-header-container', '')}
						role="row"
					>
						{lanes.map((lane, laneIndex) => (
							<sn-lane-header
								id={lane.id}
								lane={lane}
								key={`lane_header_${lane.id}`}
								cards={getCardsForLane(lane, cards)}
								draggable={get(
									laneConfigOverride,
									`${lane.id}.draggable`,
									globalLaneDraggable
								)}
								tag-lane-header={get(
									laneConfigOverride,
									`${lane.id}.tag_lane_header`,
									globalLaneHeaderTag
								)}
								wrapper-dom-events={wrapperDomEvents}
								lane-index={laneIndex}
								lanes-count={lanes.length}
								swimlanes={swimlanes}
								custom-scroll-options={customScrollOptions}
								hook-insert={(e) => {
									dispatch(REGISTER_WRAPPER_EVENT, {
										name: 'sn-lane-header',
										element: e.elm,
									});
								}}
							/>
						))}
					</div>
				</header>

				<main className="main-container">
					{/***********************
					 ******* 1D BOARD *******
					 ***********************/}

					<If test={!IS_SWIMLANE_BOARD}>
						<div className="lanes-container" role="rowgroup">
							{lanes.map((lane, laneIndex) => (
								<sn-lane
									id={`lane_${lane.id}`}
									lane={lane}
									key={lane.id}
									cards={getCardsForLane(lane, cards)}
									draggable={get(
										laneConfigOverride,
										`${lane.id}.draggable`,
										globalLaneDraggable
									)}
									tag-lane-header={get(
										laneConfigOverride,
										`${lane.id}.tag_lane_header`,
										globalLaneHeaderTag
									)}
									tag-lane-footer={get(
										laneConfigOverride,
										`${lane.id}.tag_lane_footer`,
										globalLaneFooterTag
									)}
									tag-drag-placeholder={get(
										laneConfigOverride,
										`${lane.id}.tag_drag_placeholder`,
										globalLaneDragPlaceholderTag
									)}
									tag-drop-placeholder={get(
										laneConfigOverride,
										`${lane.id}.tag_drop_placeholder`,
										globalLaneDropPlaceholderTag
									)}
									global-card-config={globalCardConfig}
									card-config-override={cardConfigOverride}
									wrapper-dom-events={wrapperDomEvents}
									lane-index={laneIndex}
									lanes-count={lanes.length}
									custom-scroll-options={customScrollOptions}
									hook-insert={(e) => {
										dispatch(REGISTER_WRAPPER_EVENT, {
											name: 'sn-lane',
											element: e.elm,
										});
									}}
								/>
							))}
						</div>
					</If>

					{/***********************
					 ******* 2D BOARD *******
					 ************************/}

					<If test={IS_SWIMLANE_BOARD}>
						<div className="swimlanes-container">
							<div className="swimlanes">
								{swimlanes.map((swimlane, slIndex) => (
									<sn-swimlane
										id={swimlane.id}
										key={swimlane.id}
										swimlane={swimlane}
										lanes={lanes}
										cards={getCardsForSwimlane(
											swimlane,
											cards
										)}
										collapsed={swimlane.collapsed}
										draggable={get(
											swimlaneConfigOverride,
											`${swimlane.id}.draggable`,
											globalSwimlaneDraggable
										)}
										tag-swimlane-header={get(
											swimlaneConfigOverride,
											`${swimlane.id}.tag_swimlane_header`,
											globalSwimlaneHeaderTag
										)}
										tag-drag-placeholder={get(
											swimlaneConfigOverride,
											`${swimlane.id}.tag_drag_placeholder`,
											globalSwimlaneDragPlaceholderTag
										)}
										tag-drop-placeholder={get(
											swimlaneConfigOverride,
											`${swimlane.id}.tag_drop_placeholder`,
											globalSwimlaneDropPlaceholderTag
										)}
										global-card-config={globalCardConfig}
										card-config-override={
											cardConfigOverride
										}
										lane-config-override={
											laneConfigOverride
										}
										global-lane-footer-tag={
											globalLaneFooterTag
										}
										swimlane-config-override={
											swimlaneConfigOverride
										}
										wrapper-dom-events={wrapperDomEvents}
										swimlane-index={slIndex}
										swimlanes-count={swimlanes.length}
										clone-mode={cloneMode}
										hook-insert={(e) => {
											dispatch(REGISTER_WRAPPER_EVENT, {
												name: 'sn-swimlane',
												element: e.elm,
											});
										}}
									/>
								))}
							</div>
						</div>
					</If>
				</main>

				{/*************************************
				 ***** ACCESSIBILE DND ALTERNATIVE ****
				 *************************************/}
				<If test={!cloneMode}>
					<board-accessibility
						swimlanes={swimlanes}
						lanes={lanes}
						cards={cards}
						lane-config-override={laneConfigOverride}
						options={a11yOptions}
						swimlane-config-override={swimlaneConfigOverride}
						custom-scroll-options={customScrollOptions}
					/>
				</If>
			</div>
		</div>
	);
};
