import {
	querySelectorAllDeep,
	insertAfter,
	insertBefore,
	removeNode,
	querySelectorDeep,
} from '@devsnc/library-vtb-commons';

const CUSTOM_SCROLL_BAR_EL = '.simplebar-content-wrapper';

export const addVLanePlaceholders = (index, boardEl, srcEleWidth) => {
	// For each sn-lane that has [laneIndex="index"] (which can be multiple in 2D board),
	// add empty placeholder after it In the selector below,

	const visibleLanes = querySelectorAllDeep('sn-lane', boardEl, false);
	const firstLane = visibleLanes.filter((node) => node.laneIndex === 0); // For index < 0;
	const lanes =
		index < 0
			? firstLane
			: visibleLanes.filter((node) => node.laneIndex === index);
	lanes.forEach((lane) => {
		const laneBody = querySelectorDeep('.lane-body', lane);
		const dragPlaceHolder = document.createElement('div');
		dragPlaceHolder.classList.add('v-lane-dragging-placeholder');
		if (srcEleWidth) dragPlaceHolder.style.minWidth = srcEleWidth + 'px';
		else dragPlaceHolder.style.minWidth = `${laneBody.clientWidth}px`;
		dragPlaceHolder.style.height = `${laneBody.clientHeight}px`;
		if (index < 0) insertBefore(dragPlaceHolder, lane);
		else insertAfter(dragPlaceHolder, lane);
	});
};

export const removeVLanePlaceholders = (rootEl) =>
	querySelectorAllDeep('.v-lane-dragging-placeholder', rootEl, false).forEach(
		removeNode
	);

export const getDraggedLane = (laneId, rootEl) =>
	querySelectorDeep(`[id="lane_${laneId}"]`, rootEl);

export const getScrollTopOfLane = (laneId, customScroll, boardEl) => {
	let laneScrollTop = 0;
	let dragElem = getDraggedLane(laneId, boardEl);
	if (dragElem) {
		const scrollElSelector = customScroll
			? CUSTOM_SCROLL_BAR_EL
			: '.sn-lane';
		const scrollEl = querySelectorDeep(scrollElSelector, dragElem);
		if (scrollEl) laneScrollTop = scrollEl.scrollTop;
	}
	return laneScrollTop;
};
