import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './sn-lane.scss';
import '../sn-card-wrapper/sn-card-wrapper';
import If from '../shared/if';
import get from 'lodash/get';
import { getStyles } from '../../shared/utils';
import customScroll from '../../behaviors/custom-scrollbar/custom-scroll';
import cardDragBehavior from '../../behaviors/sortable/card-sort';
import rtlBehavior from '@servicenow/behavior-rtl';
import { REGISTER_WRAPPER_EVENT } from '../../constants/actions';

const view = ({ properties }, { dispatch }) => {
	const {
		id,
		lane,
		cards,
		tagLaneFooter,
		globalCardConfig,
		cardConfigOverride,
		laneIndex, // for a11y
		lanesCount, // for a11y,
		swimlane,
	} = properties;

	return (
		// TODO: Refactor this view
		<div className="sn-lane" style={getStyles(lane, 'sn-lane')}>
			<div
				className="lane-body"
				style={getStyles(lane, 'lane-body')}
				role="row"
				// aria-owns={swimlane ? `sl_header_${swimlane.id}` : ``}
			>
				<div
					role="gridcell"
					aria-label={t(
						`Cards belonging to lane: ${lane.title}${
							swimlane ? `, swimlane: ${swimlane.title}` : ''
						}`
					)}
				>
					<div
						role={cards.length ? 'list' : ''}
						className="list-elem"
					>
						<If test={cards && cards.length}>
							{cards.map((card, cardIndex) => (
								<sn-card-wrapper
									id={card.id}
									key={card.id}
									role="listitem"
									draggable={get(
										cardConfigOverride,
										`${card.id}.draggable`,
										get(globalCardConfig, 'draggable')
									)}
									drag-placeholder-tag={get(
										cardConfigOverride,
										`${card.id}.drag_placeholder_tag`,
										get(
											globalCardConfig,
											'drag_placeholder_tag'
										)
									)}
									drop-placeholder-tag={get(
										cardConfigOverride,
										`${card.id}.drop_placeholder_tag`,
										get(
											globalCardConfig,
											'drop_placeholder_tag'
										)
									)}
									aria-label={t(
										`Card ${cardIndex + 1} out of ${
											cards.length
										}. ${card.title}. In lane: ${
											lane.title
										}${
											swimlane
												? `, In swimlane: ${swimlane.title}`
												: ``
										}`
									)}
									hook-insert={(e) => {
										dispatch(REGISTER_WRAPPER_EVENT, {
											name: 'sn-card-wrapper',
											element: e.elm,
										});
									}}
								>
									<If
										test={get(
											cardConfigOverride,
											`${card.id}.tag`,
											get(globalCardConfig, 'tag')
										)}
									>
										{((card) => {
											const cardComp = {
												tag: get(
													cardConfigOverride,
													`${card.id}.tag`,
													get(globalCardConfig, 'tag')
												),
											};
											card = {
												...card,
												orderPerLaneInSwimlane:
													cardIndex + 1,
												countPerLaneInSwimlane:
													cards.length,
											};
											return (
												// dot required to resolve tag name
												<cardComp.tag
													slot={card.id}
													data={card}
													{...card}
												/>
											);
										})(card)}
									</If>
								</sn-card-wrapper>
							))}
						</If>
					</div>
				</div>
			</div>
			<div
				className="lane-footer-container"
				role="row"
				id={`lane_footer_${id}`}
				aria-label={t(
					`Lane Footer, ${
						laneIndex + 1
					} out of ${lanesCount}, In lane: ${lane.title}${
						swimlane ? `, In swimlane: ${swimlane.title}` : ``
					}`
				)}
				// aria-owns={swimlane ? `sl_header_${swimlane.id}` : ``}
				style={getStyles(lane, 'lane-footer-container')}
				hook-insert={(e) => {
					dispatch(REGISTER_WRAPPER_EVENT, {
						name: 'lane-footer-container',
						element: e.elm,
					});
				}}
			>
				<If test={tagLaneFooter}>
					{/* dot required for tag to be resolved */}
					<properties.tagLaneFooter
						data={lane}
						swimlane={swimlane}
						{...lane}
						role="gridcell"
					/>
				</If>
			</div>
		</div>
	);
};

export default createCustomElement('sn-lane', {
	view,
	properties: {
		id: { default: '', reflect: true },
		lane: { default: {} },
		swimlaneId: { default: '' },
		cards: { default: [] },
		tagLaneHeader: { default: '' },
		tagLaneFooter: { default: '' },
		tagDragPlaceholder: { default: '' },
		tagDropPlaceholder: { default: '' },
		draggable: { default: true },
		globalCardConfig: { default: {} },
		cardConfigOverride: { default: {} },
		laneIndex: { default: '' },
		lanesCount: { default: '' },
		customScrollOptions: { default: {} },
		swimlane: { default: null },
	},
	behaviors: [
		cardDragBehavior,
		{
			behavior: customScroll,
			options: {
				scrollableEle: '.sn-lane',
			},
		},
		{
			behavior: rtlBehavior,
		},
	],
	styles,
});
