import { closestDeep } from '@devsnc/library-vtb-commons';
import { collapseSwimlanesAtElement, expandSwimlanes } from './collapse';

const SL_COLLAPSE_INIT_DELAY = 250;

export default function swimlaneCollapse(host) {
	let collapseTimer = null;
	let hostClientRect = {};
	
	const handleCollapse = () => {
		// save the sl-header client rect as soon as mouse down to handle immediate dragging.
		// this is required because, sortable makes element display none on drag start.
		hostClientRect.top = host.getBoundingClientRect().top;
		hostClientRect.height = host.getBoundingClientRect().height;

		// Listen to mousemove to handle the immediate drag of lane.
		host.addEventListener('mousemove', collapseNow);

		// If there is no immediate drag, collapse only after a delay
		collapseTimer = setTimeout(
			collapseNow,
			SL_COLLAPSE_INIT_DELAY
		);
	};

	const collapseNow = () => {
		resetImmediateDrag();
		collapseSwimlanesAtElement(host, hostClientRect);

		// Expand swimlanes on mouse up
		window.addEventListener('mouseup', resetSwimlanesPosition);
	};

	const resetSwimlanesPosition = () => {
		window.removeEventListener('mouseup', resetSwimlanesPosition);
		const slContainerEl = closestDeep(host, '.swimlanes');
		expandSwimlanes(slContainerEl);
	}

	const resetImmediateDrag = () => {
		host.removeEventListener('mousemove', collapseNow);
		// Cancel in-queue collapse, if already called.
		if (collapseTimer) clearTimeout(collapseTimer);
		collapseTimer = null;
	};

	return {
		addBehaviour() {
			host.addEventListener('mousedown', handleCollapse);
			host.addEventListener('mouseup', resetImmediateDrag);
		},
		removeBehaviour() {
			host.removeEventListener('mousedown', handleCollapse);
			host.removeEventListener('mouseup', resetImmediateDrag);
		},
	};
}
