import { createCustomElement } from '@servicenow/ui-core';
import { getCardsForLane } from '../../shared/utils';
import styles from './sn-swimlane.scss';
import get from 'lodash/get';
import { t } from 'sn-translate';
import { REGISTER_WRAPPER_EVENT } from '../../constants/actions';

export default createCustomElement('sn-swimlane', {
	view({ properties }, { dispatch }) {
		const {
			swimlane,
			lanes,
			cards,
			draggable,
			collapsed,
			tagSwimlaneHeader,
			globalLaneFooterTag,
			globalCardConfig,
			cardConfigOverride,
			laneConfigOverride,
			swimlanesCount,
			swimlaneIndex,
		} = properties;

		return (
			<div
				className="sn-swimlane"
				role="rowgroup"
				// aria-owns={lanes.map(
				// 	(lane) =>
				// 		`${getCardsForLane(lane, cards).map(
				// 			(card) => ` card_${card.id} `
				// 		).join(' ')} lane_footer_${swimlane.id}_${lane.id}`
				// ).join(' ')}
			>
				{/**** SWIM LANE HEADER*****/}
				<sn-swimlane-header
					id={`sl_header_${swimlane.id}`}
					key={`sl_header_${swimlane.id}`}
					className="swimlane-header"
					collapsed={collapsed}
					swimlane={swimlane}
					draggable={draggable}
					tag-swimlane-header={tagSwimlaneHeader}
					swimlane-index={swimlaneIndex}
					swimlanes-count={swimlanesCount}
					role="rowheader"
					aria-label={t(
						'Swimlane Header, {0} out of {1}: {2}',
						swimlaneIndex + 1,
						swimlanesCount,
						swimlane.name
					)}
					aria-expanded={`${!collapsed}`}
					rowspan="2"
					hook-insert={(e) => {
						dispatch(REGISTER_WRAPPER_EVENT, {
							name: 'sn-swimlane-header',
							element: e.elm,
						});
					}}
				/>

				<div className={`swimlane-body ${collapsed ? 'hide' : ''}`}>
					{lanes.map((lane, laneIndex) => (
						<sn-lane
							id={`${swimlane.id}_${lane.id}`}
							key={`${swimlane.id}_${lane.id}`}
							lane={lane}
							swimlane-id={swimlane.id}
							cards={getCardsForLane(lane, cards)}
							/* no 'tag-lane-header' prop here, because of 2D layout */
							tag-lane-footer={get(
								laneConfigOverride,
								`${lane.id}.tag_lane_footer`,
								globalLaneFooterTag
							)}
							global-card-config={globalCardConfig}
							card-config-override={cardConfigOverride}
							lane-config-override={laneConfigOverride}
							lane-index={laneIndex}
							lanes-count={lanes.length}
							swimlane={swimlane}
							hook-insert={(e) => {
								dispatch(REGISTER_WRAPPER_EVENT, {
									name: 'sn-lane',
									element: e.elm,
								});
							}}
						/>
					))}
				</div>
			</div>
		);
	},
	properties: {
		id: { default: '', reflect: true },
		swimlane: { default: {} },
		config: { default: {} },
		cards: { default: [] },
		lanes: { default: [] },
		draggable: { default: true, reflect: true },
		collapsed: { default: false },
		tagSwimlaneHeader: { default: '' },
		globalLaneFooterTag: { default: '' },
		globalCardConfig: { default: {} },
		cardConfigOverride: { default: {} },
		laneConfigOverride: { default: {} },
		tagDragPlaceholder: { default: '' },
		tagDropPlaceholder: { default: '' },
		swimlaneIndex: { default: '' },
		swimlanesCount: { default: '' },
	},
	styles,
});
