import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-modal';
import '@servicenow/now-dropdown';
import '../../shared/rearrange-list/rearrange-list';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';
import { pickValue } from '../../../shared/utils';
import { ACTION_PREFIX, CARD_MOVED } from '../../../constants/actions';
import styles from './card-move-modal.scss';
import {
	MOVE_CARD,
	VERTICAL_LANE,
	SELECT_LOCATION,
	ARRANGE_CARD_ORDER,
	CURRENT,
	CANCEL,
	SWIM_LANE,
} from '../../../constants/translated';
import If from '../../shared/if';

const orderSort = (a, b) => pickValue(a.order) - pickValue(b.order);
const getCardFromId = (cards, cardId) =>
	cards.find((card) => card.id === cardId);
const isLaneDroppable = (laneId, laneConfigOverride) => {
	return laneConfigOverride && laneConfigOverride[laneId]
		? laneConfigOverride[laneId]['droppable'] !== false
		: true;
};

const getCardPositionInLane = (cards, cardId, laneId, swimlaneId) => {
	return cards
		.filter(
			(card) =>
				card.lane_id === laneId && card.swim_lane_id === swimlaneId
		)
		.sort(orderSort)
		.findIndex((card) => card.id === cardId);
};

const getLaneMoveOptions = (lanes, currentLaneId, laneConfigOverride) => {
	if (!currentLaneId) return lanes;
	return lanes
		.filter((lane) => {
			return (
				isLaneDroppable(lane.id, laneConfigOverride) ||
				lane.id === currentLaneId
			);
		})
		.map((lane) => {
			return {
				id: lane.id,
				label:
					lane.title +
					(lane.id === currentLaneId ? ` (${CURRENT})` : ''),
			};
		});
};

const getCardMoveOptions = (
	allCards,
	newLaneId,
	newSwimlaneId,
	currentCard
) => {
	if (!allCards || !newLaneId || !currentCard) return [];

	const cardRearrangeOptions = [];
	if (
		currentCard.lane_id !== newLaneId ||
		(newSwimlaneId && currentCard.swim_lane_id !== newSwimlaneId)
	) {
		cardRearrangeOptions.push({
			displayValue: `${currentCard.title} (${CURRENT})`,
			value: currentCard.id,
			selected: true,
		});
	}

	const otherCardOptions = allCards
		.filter((card) => {
			if (newSwimlaneId) {
				return (
					card.lane_id === newLaneId &&
					card.swim_lane_id === newSwimlaneId
				);
			} else {
				return card.lane_id === newLaneId;
			}
		})
		.sort(orderSort)
		.map((card) => {
			let cardTitle = card.title;
			if (card.id === currentCard.id) cardTitle += ` (${CURRENT})`;
			return {
				displayValue: cardTitle,
				value: card.id,
				selected: card.id === currentCard.id,
			};
		});

	return cardRearrangeOptions.concat(otherCardOptions);
};

export default createCustomElement('card-move-modal', {
	view(state) {
		const {
			properties: {
				lanes,
				swimlanes,
				cards,
				selectedCard,
				laneConfigOverride,
				swimlaneConfigOverride,
			},
			selectedLaneId,
			selectedSwimlaneId,
			cardMoveOptions,
		} = state;

		const currentCard = getCardFromId(cards, selectedCard);
		if (!currentCard) return;

		let currentLaneId = selectedLaneId || currentCard.lane_id;
		const currentLaneOptions = getLaneMoveOptions(
			lanes,
			currentCard.lane_id,
			laneConfigOverride
		);

		let currentSwimlaneId = null;
		let currentSwimlaneOptions = [];
		if (swimlanes && swimlanes.length > 0) {
			currentSwimlaneId = selectedSwimlaneId || currentCard.swim_lane_id;
			currentSwimlaneOptions = getLaneMoveOptions(
				swimlanes,
				currentCard.swim_lane_id,
				swimlaneConfigOverride
			);
		}

		const currentCardOptions =
			cardMoveOptions ||
			getCardMoveOptions(
				cards,
				currentLaneId,
				currentSwimlaneId,
				currentCard
			);

		const modalTitle = `${MOVE_CARD}: ${currentCard.title}`;
		const footerActions = [
			{ id: 'move_card', variant: 'primary', label: MOVE_CARD },
			{ id: 'cancel', variant: 'secondary', label: CANCEL },
		];

		return (
			<now-modal
				opened
				size="lg"
				header-label={modalTitle}
				footer-actions={footerActions}
			>
				<p className="modal-sub-heading">{SELECT_LOCATION}</p>
				<div className="select-lanes">
					<div className="select-lane">
						<p className="select-label">{VERTICAL_LANE}</p>
						<now-dropdown
							className="vlane-dropdown"
							items={currentLaneOptions}
							append-to-payload={{ laneType: 'LANE' }}
							selected-items={[currentLaneId]}
							config-aria={{ 'aria-label': VERTICAL_LANE }}
							variant="tertiary"
							select="single"
							size="md"
						/>
					</div>
					<If test={swimlanes.length > 0}>
						<div className="select-lane">
							<p className="select-label">{SWIM_LANE}</p>
							<now-dropdown
								items={currentSwimlaneOptions}
								append-to-payload={{ laneType: 'SWIMLANE' }}
								selected-items={[currentSwimlaneId]}
								config-aria={{ 'aria-label': SWIM_LANE }}
								variant="tertiary"
								select="single"
								size="md"
							/>
						</div>
					</If>
				</div>
				<p className="select-label">{ARRANGE_CARD_ORDER}</p>
				<div className="lanes-rearrange-container">
					<rearrange-list
						config-aria={{ 'aria-label': ARRANGE_CARD_ORDER }}
						items={currentCardOptions}
					/>
				</div>
			</now-modal>
		);
	},
	initialState: {
		updatedOrder: -1,
		selectedLaneId: '',
		selectedSwimlaneId: '',
		cardMoveOptions: null,
	},
	properties: {
		lanes: { default: {} },
		swimlanes: { default: {} },
		cards: { default: {} },
		selectedCard: { default: '' },
		laneConfigOverride: { default: {} },
		swimlaneConfigOverride: { default: {} },
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ host }) => {
			setTimeout(() => {
				const dropdownEl = querySelectorDeep('.vlane-dropdown', host);
				if (dropdownEl) dropdownEl.focus();
			}, 100);
		},
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': ({
			state,
			action: { payload },
			updateState,
		}) => {
			const {
				properties: { cards, swimlanes, selectedCard },
				selectedSwimlaneId,
				selectedLaneId,
			} = state;
			const currentCard = getCardFromId(cards, selectedCard);
			if (payload.laneType === 'LANE') {
				const _selectedLaneId = payload.value[0];
				const _selectedSwimlaneId =
					swimlanes.length > 0
						? selectedSwimlaneId || currentCard.swim_lane_id
						: null;
				const cardMoveOptions = getCardMoveOptions(
					cards,
					_selectedLaneId,
					_selectedSwimlaneId,
					currentCard
				);
				const updatedOrder =
					currentCard.lane_id !== _selectedLaneId ? 0 : -1;
				updateState({
					selectedLaneId: _selectedLaneId,
					cardMoveOptions,
					updatedOrder,
				});
			} else if (payload.laneType === 'SWIMLANE') {
				const _selectedSwimlaneId = payload.value[0];
				const currentCard = getCardFromId(cards, selectedCard);
				const cardMoveOptions = getCardMoveOptions(
					cards,
					selectedLaneId || currentCard.lane_id,
					_selectedSwimlaneId,
					currentCard
				);
				const updatedOrder =
					currentCard.swim_lane_id !== _selectedSwimlaneId ? 0 : -1;
				updateState({
					selectedSwimlaneId: _selectedSwimlaneId,
					cardMoveOptions,
					updatedOrder,
				});
			}
		},
		'NOW_MODAL#OPENED_SET': ({ dispatch }) =>
			dispatch('NOW_VISUAL_BOARD#A11Y#OPTIONS_RESET'),
		'NOW_MODAL#FOOTER_ACTION_CLICKED': ({ action, state, dispatch }) => {
			const actionType = action.payload.footerAction.id;
			const {
				properties: { cards, selectedCard },
			} = state;
			const card = getCardFromId(cards, selectedCard);
			const fromPosition = getCardPositionInLane(
				cards,
				card.id,
				card.lane_id,
				card.swim_lane_id
			);
			const toPosition =
				state.updatedOrder >= 0 ? state.updatedOrder : fromPosition;
			if (actionType === 'move_card') {
				dispatch(ACTION_PREFIX + CARD_MOVED, {
					id: card.id,
					fromLane: card.lane_id,
					fromSwimlane: card.swim_lane_id,
					fromPosition,

					toLane: state.selectedLaneId || card.lane_id,
					toPosition,
					toSwimlane: state.selectedSwimlaneId || card.swim_lane_id,

					eventType: 'keyboard',
				});
			}
			dispatch('NOW_VISUAL_BOARD#A11Y#OPTIONS_RESET');
		},
		'NOW_VISUAL_BOARD#REARRANGE_LIST#ORDER_CHANGED': ({
			action,
			updateState,
		}) => {
			updateState({
				updatedOrder: action.payload.updatedIndex,
				cardMoveOptions: action.payload.updatedItems,
			});
		},
	},
	styles,
});
