import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-card-wrapper.scss';

export default createCustomElement('sn-card-wrapper', {
	view(state) {
		const {
			properties: { id, draggable },
		} = state;
		return (
			<div
				className={`card-wrapper ${draggable ? 'draggable' : ''}`}
				id={id}
			>
				<slot name={id}></slot>
			</div>
		);
	},
	properties: {
		id: { default: '', reflect: true },
		draggable: { default: true },
		dragPlaceholderTag: { default: '' },
		dropPlaceholderTag: { default: '' },
	},
	behaviors: [],
	styles,
});
