import { actionTypes } from '@servicenow/ui-core';
import { getComponentsByTagNames } from '@devsnc/library-uxf';
import { getTagsToImport } from '../shared/utils';
import without from 'lodash/without';
import { TAG_PROPERTIES } from '../constants/constants';

export default {
	['IMPORT_UXF_TAGS']: ({ updateState, action }) => {
		const {
			payload: { tags },
		} = action;
		updateState(({ state }) => {
			const importedTags = state.importedTags || [];
			const newTags = without(tags, ...importedTags);
			if (newTags.length) {
				// We don't have to update anything in success case, as the tags are already pushed into importedtags
				getComponentsByTagNames(newTags).catch((err) => {
					// reset to inital importedTags before call
					updateState({
						path: 'importedTags',
						value: importedTags,
						operation: 'set',
					});
					console.error(
						'Component fetching from UXF registry failed',
						err
					);
				});
			}

			return {
				path: 'importedTags',
				value: [...importedTags, ...newTags],
				operation: 'set',
			};
		});
	},
	[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
		const { loadTagsFromUxf } = properties;
		if (loadTagsFromUxf) {
			const tags = getTagsToImport(properties);
			dispatch('IMPORT_UXF_TAGS', { tags });
		}
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
		action,
		properties,
		dispatch,
	}) => {
		const { loadTagsFromUxf } = properties;
		const {
			payload: { name: propChanged },
		} = action;
		if (loadTagsFromUxf && TAG_PROPERTIES.includes(propChanged)) {
			const tags = getTagsToImport(properties);
			dispatch('IMPORT_UXF_TAGS', { tags });
		}
	},
};
