import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import If from '../shared/if';
import styles from './sn-swimlane-header.scss';
import { getStyles } from '../../shared/utils';
import swimlaneCollapseBehavior from '../../behaviors/swimlane-collapse/behavior';

export default createCustomElement('sn-swimlane-header', {
	view({ properties }) {
		const {
			draggable,
			swimlane,
			tagSwimlaneHeader,
			collapsed,
		} = properties;
		if (!swimlane || !swimlane.id) return null;
		return (
			<div
				id={`swimlane_header_${swimlane.id}`}
				draggable={draggable}
				className={`sn-swimlane-header ${draggable ? 'draggable' : ''}`}
				style={getStyles(swimlane, 'sn-swimlane-header')}
			>
				<div className="swimlane-header-element-wrapper">
					<If test={tagSwimlaneHeader}>
						{/*dot required to resolve tag name*/}
						<properties.tagSwimlaneHeader
							data={swimlane}
							collapsed={collapsed}
							{...swimlane}
						/>
					</If>
				</div>
			</div>
		);
	},
	behaviors: [swimlaneCollapseBehavior],
	properties: {
		id: { default: '', reflect: true },
		swimlane: { default: {} },
		tagSwimlaneHeader: { default: '' },
		draggable: { default: true, reflect: true },
		collapsed: { default: false },
		swimlaneIndex: { default: '' },
		swimlanesCount: { default: '' },
		tagDragPlaceholder: { default: '' },
		tagDropPlaceholder: { default: '' },
	},
	styles,
});
