import { querySelectorDeep } from '@devsnc/library-vtb-commons';

export default function ({ host }) {
	const props = host.getProperties();
	const { horizontalDragDirection } = props;
	const laneCloneDiv = querySelectorDeep('.lane-clone', host);
	if (laneCloneDiv && horizontalDragDirection) {
		if (!laneCloneDiv.classList.contains(horizontalDragDirection))
			laneCloneDiv.classList.add(horizontalDragDirection);

		if (horizontalDragDirection === 'horizontal-dir-right') {
			if (laneCloneDiv.classList.contains('horizontal-dir-left'))
				laneCloneDiv.classList.remove('horizontal-dir-left');
		}

		if (horizontalDragDirection === 'horizontal-dir-left') {
			if (laneCloneDiv.classList.contains('horizontal-dir-right'))
				laneCloneDiv.classList.remove('horizontal-dir-right');
		}
	}
}
