export const ACTION_PREFIX = 'NOW_VISUAL_BOARD#';
export const INTERNAL_ACTION_PREFIX = ACTION_PREFIX + 'INTERNAL#';
/**
 * Fired when a card is moved from one lane to another lane.
 * The move can be triggered either by drag-and-drop or any alternative
 * accessibility approach.
 *
 * @param {String} card - the id of the card
 * @param {String} fromLane - the id of the source lane
 * @param {String} toLane - the id of the destination lane
 * @param {Integer} fromPosition - the index of the card in the source lane
 * @param {Integer} toPosition - the index of the card in the destination lane
 * */
export const CARD_MOVED = 'CARD_MOVED';

/**
 * Fired when a lane is dragged from one position to another.
 * The move can be triggered either by drag-and-drop or any alternative
 * accessibility approach.
 *
 * @param {String} lane - the id of lane
 * @param {Integer} fromPosition - the source index position
 * @param {Integer} toPosition - the destination index position
 * */
export const LANE_MOVED = 'LANE_MOVED';

/**
 * Fired when a swimlane is dragged from one position to another.
 * The move can be triggered either by drag-and-drop or any alternative
 * accessibility approach.
 *
 * @param {String} swimlane - the id of swimlane
 * @param {Integer} fromPosition - the source index position
 * @param {Integer} toPosition - the destination index position
 * */
export const SWIMLANE_MOVED = 'SWIMLANE_MOVED';

/**
 * Fired when a card is dragged from its position in some lane
 * @param {String} card - the id of the card
 * @param {String} lane - the id of the lane
 * @param {Integer} fromPosition - the index of the card in the current lane
 * */
export const CARD_SORT_START = 'CARD_SORT_START';

/**
 * Fired when the card's dragging ends
 * @param {String} card - the id of the card
 * @param {String} fromLane - the id of the source lane of the card
 * @param {String} toLane - the id of the destination lane of the card
 * @param {Integer} fromPosition - the index of the card in the source lane
 * @param {Integer} toPosition - the index of the card in the destination lane
 * */
export const CARD_SORT_END = 'CARD_SORT_END';

/**
 * Fired when the lane is dragged from its position
 * @param {String} lane - the id of the lane
 * @param {Integer} fromPosition - the source index of the lane
 * @param {Integer} toPosition - the destination index of the lane
 * */
export const LANE_SORT_START = 'LANE_SORT_START';

/**
 * Fired when the lane's dragging ends
 * @param {String} lane - the id of the lane
 * @param {Integer} fromPosition - the source index of the lane
 * @param {Integer} toPosition - the destination index of the lane
 * */
export const LANE_SORT_END = 'LANE_SORT_END';

/**
 * Fired when the swimlane's dragging starts
 * @param {String} swimlane - the id of the swimlane
 * @param {Integer} fromPosition - the source index of the swimlane
 * */
export const SWIMLANE_SORT_START = 'SWIMLANE_SORT_START';

/**
 * Fired when the swimlane's dragging ends
 * @param {String} swimlane - the id of the swimlane
 * @param {Integer} fromPosition - the source index of the swimlane
 * @param {Integer} toPosition - the destination index of the swimlane
 * */
export const SWIMLANE_SORT_END = 'SWIMLANE_SORT_END';

/**
 * Fired when the lane drag area change
 * @param {String} lane - the target element
 * @param {Integer} oldPosition - the old position of lane
 * @param {Integer} newPosition - the new position of lane
 * */
export const LANE_DROP_AREA_CHANGE = 'LANE_DROP_AREA_CHANGE';

/*************************************
 ************ ACCESSIBILITY **********
 **************************************/
/**
 * Board consumers dispatch this action to trigger the drag-and-drop alternative modal
 * Example - dispatch("NOW_VISUAL_BOARD#OPEN_CARD_MOVE_MODAL", { id: <card_id> })
 * */
export const OPEN_CARD_MOVE_MODAL = ACTION_PREFIX + 'OPEN_CARD_MOVE_MODAL';

/**
 * Board consumers dispatch this action to trigger the lane reorder modal
 * Example usage - dispatch("NOW_VISUAL_BOARD#OPEN_LANE_MOVE_MODAL", { id: <lane_id> })
 * */
export const OPEN_LANE_MOVE_MODAL = ACTION_PREFIX + 'OPEN_LANE_MOVE_MODAL';

/**
 * Board consumers dispatch this action to trigger the swimlane reorder modal
 * Example usage - dispatch("NOW_VISUAL_BOARD#OPEN_SWIMLANE_MOVE_MODAL", { id: <swim_lane_id> })
 * */
export const OPEN_SWIMLANE_MOVE_MODAL =
	ACTION_PREFIX + 'OPEN_SWIMLANE_MOVE_MODAL';

// Internal Actions
export const REGISTER_WRAPPER_EVENT = 'REGISTER_WRAPPER_EVENT';
