import {
	closestDeep,
	querySelectorDeep,
} from '@devsnc/library-vtb-commons';

const SORTABLE_AUTO_SCROLL_BOUNDS = '__auto_scroll_y_bounds__';
const SL_SCROLL_BUFFER_AREA = 200;

//TODO: Try if we can make this collapse a generic behavior and move to sortable
export function collapseSwimlanesAtElement(host, hostClientRect) {
	const slContainerEl = closestDeep(host, '.swimlanes');
	const boardContainerEl = closestDeep(slContainerEl, '.sn-board');
	const headerContainerEl = querySelectorDeep(
		'.lanes-header',
		boardContainerEl
	);

	const swimlaneIndex = host.swimlaneIndex || 0;
	const swimlanesCount = host.swimlanesCount || 0;
	const collapsedSlTop = hostClientRect.top || 0;
	const collapsedSlHeight = hostClientRect.height || 0;
	const upperItemsHeight = swimlaneIndex * collapsedSlHeight;
	const currentScroll = boardContainerEl.scrollTop;
	const containerTop =
		boardContainerEl.getBoundingClientRect().top +
		headerContainerEl.getBoundingClientRect().height;

	// calculate swimlanes container offset after collasping
	const offsetTop =
		currentScroll + (collapsedSlTop - containerTop) - upperItemsHeight;

	// set the height of container to its scroll height
	slContainerEl.style.height = `${
		slContainerEl.getBoundingClientRect().height
	}px`;

	//To animate collapse (top), set current position as top of each sn-swimlane
	const swimlaneElements = [].slice
		.call(slContainerEl.children)
		.filter((el) => el.tagName === 'SN-SWIMLANE');
	const swimlaneHeaders = [];
	swimlaneElements.reduce((totalHeight, sl) => {
		const slHeader = querySelectorDeep('sn-swimlane-header', sl);
		swimlaneHeaders.push(slHeader);
		sl.style.top = `${totalHeight}px`;
		return totalHeight + sl.getBoundingClientRect().height;
	}, 0);

	swimlaneElements.forEach((sl, index) => {
		sl.style.position = 'absolute';
		sl.style.top = offsetTop + index * collapsedSlHeight + 'px';
	});
	swimlaneHeaders.forEach((slh) => {
		slh.nextElementSibling.classList.add('hide');
		slh.collapsed = true;
	});

	// set scroll bounds to scrollParent (.boardContainer)
	// will be used by sortable library for autoscroll while sorting.
	let minScrollTop = offsetTop < currentScroll ? offsetTop : currentScroll;
	minScrollTop =
		minScrollTop > SL_SCROLL_BUFFER_AREA
			? minScrollTop - SL_SCROLL_BUFFER_AREA
			: 0;

	let maxScrollTop =
		offsetTop -
		SL_SCROLL_BUFFER_AREA +
		(swimlanesCount + 1) * collapsedSlHeight;
	maxScrollTop = maxScrollTop > currentScroll ? maxScrollTop : currentScroll;

	boardContainerEl[SORTABLE_AUTO_SCROLL_BOUNDS] = [
		minScrollTop,
		maxScrollTop,
	];
}

export function expandSwimlanes(slContainerEl) {
	// Swimlanes are already expanded if slContainerEl height is unset
	if (!slContainerEl || slContainerEl.style.height === 'unset') 
		return;

	const swimlaneElements = [].slice
		.call(slContainerEl.children)
		.filter((el) => el.tagName === 'SN-SWIMLANE');
	
	swimlaneElements.forEach((sl) => {
		sl.style.position = null;
		sl.style.top = null;
		const slh = querySelectorDeep('sn-swimlane-header', sl);
		if (slh){
			slh.collapsed = sl.collapsed;
			if (!slh.collapsed) slh.nextElementSibling.classList.remove('hide');
		}
	});
	slContainerEl.style.height = 'unset';

	const boardContainerEl = closestDeep(slContainerEl, '.sn-board');
	if (boardContainerEl) boardContainerEl[SORTABLE_AUTO_SCROLL_BOUNDS] = null;
}
