import { querySelectorDeep } from '@devsnc/library-vtb-commons';
import SimpleBar from 'simplebar';
import { actionTypes } from '@servicenow/ui-core';
import get from 'lodash/get';

export default {
	name: 'custom-scroll',
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ host, properties, action }) => {
			const {
				customScrollOptions: {
					enableScroll,
					scrollTrackCSS,
					scrollBarCSS,
				},
			} = properties;
			const { scrollableEle, hideElement } = action.payload.options;

			if (enableScroll) {
				let container = querySelectorDeep(scrollableEle, host);
				let options = {
					autoHide: get(
						properties,
						'customScrollOptions.autoHide',
						'true'
					),
					timeout: get(
						properties,
						'customScrollOptions.timeout',
						'500'
					),
					direction: host.dir,
				};
				let scroll = new SimpleBar(container, options);

				// RTL support
				container.dataset.simplebarDirection = host.dir;

				let scrollTrackEl = scroll.axis.y.track.el;
				scrollTrackEl.style.pointerEvents = 'auto';
				if (scrollTrackCSS) {
					for (let [prop, val] of Object.entries(scrollTrackCSS)) {
						scrollTrackEl.style.cssText += `${prop}: ${val};`;
					}
				}

				let scrollBarEl = scroll.axis.y.scrollbar.el;
				if (scrollBarCSS) {
					for (let [prop, val] of Object.entries(scrollBarCSS)) {
						scrollBarEl.style.cssText += `${prop}: ${val};`;
					}
				}
			}
		},
	},
};
