import {
	COMPONENT_TAG_PROPS,
	COMPONENT_TAG_OVERRIDE_PROPS,
	OVERRIDING_TAG_KEYS,
} from '../constants/constants';
import isEmpty from 'lodash/isEmpty';

export const pick = (value) => (obj) => {
	if (typeof obj === 'object' && obj.hasOwnProperty(value)) return obj[value];
	else return obj;
};

const orderSort = (a, b) => pickValue(a.order) - pickValue(b.order);

export const getCardsForLane = (lane, cards) =>
	cards.filter((card) => card.lane_id === lane.id).sort(orderSort);

export const getCardsForSwimlane = (swimlane, cards) =>
	cards.filter((card) => card.swim_lane_id === swimlane.id);

export const getStyles = (lane, elemName) => {
	return lane['_style_'] ? lane['_style_'][elemName] : {};
};

export const pickLabel = pick('label');
export const pickValue = pick('value');

/**
 * Combines and returns all the component tags that are passed in the properties. These tags can be
 * meant for cards, lanes and swimlanes. Board also supports global tags, that are applicable for all items,
 * along with overriding tags, which can be specific to individual items, like cards or lanes.
 * See "cardConfigOverride" prop for an example.
 * */
export const getTagsToImport = (boardProps) => {
	// global tags
	const tags = COMPONENT_TAG_PROPS.map(
		(tagName) => boardProps[tagName]
	).filter((tag) => !isEmpty(tag));

	// overriding tags
	const overrideObjs = COMPONENT_TAG_OVERRIDE_PROPS.map(
		(overrideProp) => boardProps[overrideProp]
	);

	overrideObjs.forEach((override) => {
		Object.keys(override).forEach((itemId) => {
			const config = override[itemId];
			// To keep it short, loop all the OVERRIDING_TAG_KEYS to consider overrides of cards, lanes and swimlanes
			OVERRIDING_TAG_KEYS.forEach((tagStr) => {
				if (config[tagStr] && tags.indexOf(config[tagStr]) === -1)
					tags.push(config[tagStr]);
			});
		});
	});
	return tags;
};
