import { nowSortable } from '@devsnc/library-vtb-commons';
import {
	addVLanePlaceholders,
	removeVLanePlaceholders,
} from '../../components/sn-lane/dragHelper';
import { closestDeep, querySelectorDeep } from '@devsnc/library-vtb-commons';
import '../../components/lane-clone/lane-clone';

export default {
	behavior: nowSortable,
	options: {
		items: 'sn-lane-header',
		dropzone: '.lanes-header-container',
		actionPrefix: 'LANE_',
		xScrollParent: '.sn-board',
		yScrollParent: 'body',
		cloneAppendTo: '.board-container',
		listOrder: 'horizontal',
		restrictTo: '.sn-board',
		keyboardSupport: false,
		lockAxis: 'x',
		itemsFilter: (item) => item.draggable,
		cursorChecker: () => '',
		clone: (el) => {
			const boardEl = closestDeep(el, 'now-visual-board');
			const clone = document.createElement('lane-clone');
			clone.props = boardEl.getProperties();
			clone.props.id = el.id;
			clone.props.boardEl = boardEl;
			clone.props.height = boardEl.getBoundingClientRect().height;
			return clone;
		},
		condition: (host) => {
			// Don't add sortable behavior if cloneMode is true
			return !host.cloneMode;
		},
		dynamicConfig: (el) => {
			const consumerConfig = {};
			const props = el.getProperties();
			const { laneDragPlaceholderTag, laneDropPlaceholderTag } = props;
			if (laneDragPlaceholderTag)
				consumerConfig.tagSourcePlaceholder = laneDragPlaceholderTag;
			if (laneDropPlaceholderTag)
				consumerConfig.tagDestPlaceholder = laneDropPlaceholderTag;
			return consumerConfig;
		},
		onDestPlaceholderInsert: (
			targetElement,
			srcPlaceholder,
			destPlaceholder,
			newPosition
		) => {
			const boardEl = closestDeep(targetElement, 'now-visual-board');
			const snBoardElem = querySelectorDeep('.sn-board', boardEl);
			const { laneIndex } = targetElement.getProperties();
			removeVLanePlaceholders(snBoardElem);

			if (newPosition < 0 || newPosition === laneIndex) return;

			const newPlaceholderPos =
				laneIndex > newPosition ? newPosition - 1 : newPosition;
			const { width: srcEleWidth } = srcPlaceholder.getProperties();
			addVLanePlaceholders(newPlaceholderPos, snBoardElem, srcEleWidth);
		},
	},
};
