import {
	OPEN_CARD_MOVE_MODAL,
	OPEN_LANE_MOVE_MODAL,
	OPEN_SWIMLANE_MOVE_MODAL,
} from '../constants/actions';

export default {
	[OPEN_CARD_MOVE_MODAL]: ({ action: { payload }, updateState }) => {
		updateState({
			a11yOptions: {
				cardId: payload.id,
				action: 'CARD_MOVE',
			},
		});
	},
	[OPEN_LANE_MOVE_MODAL]: ({ action: { payload }, updateState }) => {
		updateState({
			a11yOptions: {
				laneId: payload.id,
				action: 'LANE_MOVE',
			},
		});
	},
	[OPEN_SWIMLANE_MOVE_MODAL]: ({ action: { payload }, updateState }) => {
		updateState({
			a11yOptions: {
				laneId: payload.id,
				action: 'SWIM_LANE_MOVE',
			},
		});
	},
};
